import { getNationFields } from '@/service/api/internationalization'
export default {
  data () {
    return {
      national_fields: [],
    }
  },
  methods: {
    async national (table_names) {
      const res = await getNationFields({
        org_level_uuid: this.$store.state.userData.org_level_uuid,
        table_names
      })
      if (res.code == 200) {
        this.national_fields = res.data.Data
      }
    },
    judegNational (field) {
      return this.national_fields.includes(field)
    },
    // 处理国际化字段函数
    INT (arr) {
      if (!arr) return
      const type = Object.prototype.toString.call(arr)
      if (type.indexOf('String') > 0) {
        return arr
      }
      let newArr = []
      if (arr.length <= 0) {
        return ''
      }
      arr.forEach((item, index) => {
        newArr.push({})
        for (let i in item) {
          if (item[i] && i != 'uuid' && i != 'language_code') {
            newArr[index][i] = item[i]
          }
        }
      })
      // console.log(newArr);
      const keys = newArr.map(item => {
        return Object.keys(item)[0]
      })
      const values = newArr.map(item => {
        return Object.values(item)[0]
      })
      const currentLanguageIndex = keys.findIndex(item => item == this.$store.state.lang)
      return values[currentLanguageIndex]
    }
  }
}