<template>
  <div>
    <mrc_ess_svgIcon v-for="item in svgList"
                     :key='item'
                     :icon-class='item'></mrc_ess_svgIcon>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    iconList: {
      type: [String, Array]
    }
  },
  data () {
    return {

    };
  },
  validations: {},
  computed: {
    svgList () {
      const type = Object.prototype.toString.call(this.iconList)
      if (type.indexOf('array') > -1) {
        return this.iconList
      } else if (type.indexOf('string') > -1) {
        const arr = this.iconList.split(',')
        return arr
      } else {
        return []
      }
    }
  },
  created () { },
  watch: {},
  mounted () { },

  methods: {

  },
};
</script>

