<template>
  <!-- 普通参照 -->
  <div class="dictNameBox"
       style="width: 100%">
    <div v-show="!disabled">
      <v-select v-if="!skLoading"
                :value="dictName"
                :readonly="disabled"
                :items="dict_arr"
                :no-data-text="noDataText"
                :item-value='dict_value'
                :item-text='dict_label'
                :label="label"
                :multiple="multiple"
                @change='dictNameChange'
                :chips='chips'
                :clearable="clearable"
                :error-messages="errMsg"
                :placeholder='placeholder'
                :rules="myRules"></v-select>
      <v-select v-else></v-select>
    </div>
    <mrc_ess_input :error-messages="errMsg"
                   v-show="disabled"
                   :label="label"
                   readonly
                   :placeholder='placeholder'
                   v-model='showName'>
    </mrc_ess_input>
  </div>
</template> 
  
<script>
/**
 * info 公用普通参照组件 可传单个dict_name 或者 对象形式 多个参数
 * @params label 标签名
 * @params disabled 是否禁用
 * @params obj 请求的条件 dict_name 等
 * @params noDataText 没有下拉显示的文字
 * @params select_arr 下拉的数据
 * @params multiple 多选
 * @params rules 为空时显示的字，默认为 必填
 * @params clearable 清空
 * @params chips 纸片
 * @params errMsg 手动提示错误消息 rules必须传false
 * @params dict_name 只有一个参照名字的参数时就可能不用传obj直接传名字
 * @event  getData  select 改变的事件
 * 使用 <mrc_ess_mrc_ess_dictName></mrc_ess_dictName> 
 */
import { get_dict_name } from '@/service/api/data_dictionary'
export default {
  components: {},
  props: {
    placeholder: {
      type: String
    },
    dict_value: {
      type: String,
      default: 'dict_value'
    },
    dict_label: {
      type: String,
      default: 'dict_label'
    },
    errMsg: {
      type: Array,
      default: () => []
    },
    clearable: {
      type: Boolean,
      default: false
    },
    chips: {
      type: Boolean,
      default: true
    },

    dict_name: {
      type: String
    },
    label: {
      type: String
    },
    disabled: {
      type: Boolean
    },
    obj: {
      type: Object,
      default: () => { }
    },
    noDataText: {
      type: String
    },
    select_arr: {
      type: Array
    },
    multiple: {
      type: Boolean
    },
    rules: {
      type: [String, Boolean]
    },
    dictName: {
      type: [Array, String]
    },
  },
  data () {
    return {
      dict_arr: [],
      requestData: {},
      skLoading: true,
      showName: ''
    };
  },
  model: {
    prop: 'dictName',
    event: 'getData'
  },
  created () {

  },
  async mounted () {
    this.dict_arr = this.select_arr
    this.skLoading = true
    if (typeof this.obj == 'string') {
      const dictName = await getBusinessList({ dict_name: this.obj })
      if (dictName.code == 200) {
        this.dictName_arr = dictName.data
        this.$emit('getDictData', this.dict_arr)
      }
    } else {
      this.requestData = this.obj ? this.obj : {}
      if (Object.keys(this.requestData).length > 0) {
        const dictName = await get_dict_name({ ...this.requestData })
        if (dictName.code == 200) {
          this.dict_arr = dictName.data
          this.$emit('getDictData', this.dict_arr)
        }
      }
    }

    if (this.dict_name) {
      const dictName = await get_dict_name({ dict_name: this.dict_name })
      if (dictName.code == 200) {
        this.dict_arr = dictName.data
        this.$emit('getDictData', this.dict_arr)
      }
    }
    this.setShowName()
    this.skLoading = false
  },
  methods: {
    dictNameChange (val) {
      this.setShowName()
      this.$emit('getData', val)
    },
    setShowName () {
      if (Object.prototype.toString.call(this.dictName).indexOf('Array') > -1) {
        // let index = []
        let afterDelArr = []
        let indexArr = this.dictName.reduce((totle, item) => {
          let index = this.dict_arr.findIndex(it => {


            if (it[this.dict_value] == item) {
              afterDelArr.push(item)
            }
            return it[this.dict_value] == item
          })

          totle.push(index)
          return totle
        }, [])
        this.$emit('getData', afterDelArr)
        let html = ''
        indexArr.forEach((item, index) => {

          if (item > -1) {
            html += this.dict_arr[item][this.dict_label] + (index == indexArr.length - 1 ? '' : '，')
          }
          this.showName = html
        })
      } else {
        const index = this.dict_arr.findIndex(item => {
          return item[this.dict_value] == this.dictName
        })
        if (index != -1) {
          this.showName = this.dict_arr[index][this.dict_label]
        }
      }

    }
  },
  computed: {
    myRules () {
      if (this.rules == false) {
        return []
      } else {
        return [v => !!v || this.rules]
      }
    }
  },
  watch: {
    obj: {
      async handler (val) {
        this.requestData = val
        if (typeof val == 'string') {
          const dictName = await get_dict_name({ dict_name: val })
          if (dictName.code == 200) {
            this.dict_arr = dictName.data
            this.$emit('getDictData', this.dict_arr)
          }
        } else {
          // 这里有个return 阻断执行，，报错看看这个
          for (var i in this.requestData) {
            if (i != 'dict_name') {
              if (!this.requestData[i]) return
            }
          }
          if (!this.requestData.dict_name) return
          const dictName = await get_dict_name({ ...this.requestData })
          if (dictName.code == 200) {
            this.dict_arr = dictName.data
            this.skLoading = false
          }
        }
        if (this.dict_name) {
          this.setShowName()
        }
      },
      deep: true
    },
    select_arr: {
      handler (val) {
        this.dict_arr = val
        this.setShowName()
      },
      deep: true
    }
  },
};
</script>

<style scoped>
.dictNameBox /deep/ .v-select__slot input {
  min-height: 44px;
  height: 44px;
  line-height: 44px;
  padding-left: 8px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
}
