//     store.commit('set_sideMenu', asyncRouter)
//     var routers = store.state.sideMenu
//   }
//   var currentRouter = filterRouter(routers)
//   router.addRoutes(currentRouter)
//   router.options.routes = router.options.routes.concat(currentRouter)
//   registerRouteFresh = false
