const whiteList = ['login', 'forget-password', 'reset-password']
router.beforeEach((to, from, next) => {
  // console.log(to, from);
  NProgress.start();
  const r = JSON.parse(sessionStorage.getItem('sideMenu'))
  const token = Cookie.get('token')
  if (whiteList.every(item => to.fullPath.indexOf(item) <= -1)) {

    // 没有菜单列表就去登录界面
    if (!r || r.length <= 0) {
      // store.commit('set_sideMenu', asyncRouter)
      // store.commit('newsTips', '没有菜单列表')
      // next({ name: 'login', replace: true })
    }
    // 记得复原
    if (!token) {
      store.commit('newsTips', '没有token')
      next({ path: `/login`, query: { redirect: `${to.name}` }, replace: true })
    }
    // else {
    // }
    // if (!token) {
    //   store.commit('newsTips', '没有token')
    //   next({ path: `/login`, query: { redirect: `${to.name}` }, replace: true })
    // }
    // else {
    // }
    // 将全部菜单和登录返回的菜单进行比较 不能挑战到登录外的菜单
    // console.log(asyncRouter);

    // console.log(to);
    next()
  } else {
    next()
  }
})
router.afterEach((to, from) => {
  NProgress.done();
})
export default router



function resetRouter () {
  const newRouter = new VueRouter({
    mode: 'hash',
    routes: []
  })
  router.matcher = newRouter.matcher // reset router
}



function permissionRouter (allRouter, menu) {
  // 找到对应权限的路由，并在最高级的路由添加权限 1
  allRouter.forEach(i => {
    menu.forEach(j => {
      if (j.menusTree && j.menusTree.length > 0) {
        j.menusTree.forEach(k => {
          if (k.menusTree && k.menusTree.menusTree.length > 0) {
            k.menusTree.forEach(l => {
              if (l.url == i.redirect) {
                i.meta.permission = 1
              }
            })
          } else {
            if (k.url == i.redirect) {
              i.meta.permission = 1
            }
          }

        })
      } else {
        if (j.url == i.redirect) {
          i.meta.permission = 1
        }
      }
    })
  })

  // 最顶级权限路由下的路由都添加权限
  addPermission(allRouter)
  console.log(allRouter);
}

function addPermission (router, per) {
  router.forEach(item => {
    if ((item.meta.permission == 1 || per == 1) && item.children && item.children.length > 0) {
      item.meta.permission = 1
      addPermission(item.children, 1)
    } else {
      item.meta.permission = 1
    }
  })
}
