--> 
<template>

  <div class="tableBoxComponent">
    <mrc_ess_addBtn v-if="!hideAddBtn"
                    :list='addBtnList'
                    @handleAdd='handleAdd'
                    :pageName='pageName'></mrc_ess_addBtn>
    <!-- :item-key="itemKey" -->
    <v-data-table :headers="showHeaders"
                  v-model="selected"
                  :page.sync="page"
                  :loading="isLoading"
                  @click:row='clickRow'
                  :hide-default-header="hideDefaultHeader"
                  disable-sort
                  :search="search"
                  fixed-header
                  hide-default-footer
                  :show-select="showSelect"
                  :items="items"
                  :items-per-page="options.pagesize"
                  @page-count="pageCount">
      <!-- :height="tableHeight" -->
      <template v-for="it in slotKey"
                :slot="'item.' + it"
                slot-scope="{ item }">
        <slot :name="'item.' + it"
              :item="item" />
      </template>
      <!-- <template slot='top'> -->
      <slot name="top"
            slot='top' />
      <!-- </template> -->
      <!-- <template v-slot:top>
        <slot name="top" />
      </template> -->
      <template v-slot:no-data>
        <mrc_ess_noContent />
      </template>
      <template v-slot:header.data-table-select="{ on, props }">
        <slot name="header.data-table-select"
              :on="on"
              :props="props">
          <v-simple-checkbox color="primary"
                             v-bind="props"
                             v-on="on"></v-simple-checkbox>
        </slot>
      </template>
      <template v-slot:item.data-table-select="{ isSelected, select }">
        <slot name="item.data-table-select"
              :isSelected="isSelected"
              :select="select">
          <v-simple-checkbox color="primary"
                             :value="isSelected"
                             @input="select($event)"></v-simple-checkbox>
        </slot>
      </template>
      <slot name='body'
            slot='body'
            :items="items">
      </slot>
      <slot name='header'
            slot='header'
            :header='headers'></slot>

      <!-- <slot v-for="(it) in headers"
            :slot="'item.' + it.value"
            :name="'item.' + it.value"
            slot-scope="{item}"
            :item='item'></slot> -->

      <!-- <slot name='item.name'
            slot='item.name'
            slot-scope="{item}"
            :item='item'></slot> -->

      <template slot='item.actions'
                slot-scope="{item}">
        <slot name='item.actions'
              :item='item'></slot>
      </template>

      <!-- ="{ headers }" -->
      <template v-slot:header.action>
        <v-tooltip bottom>
          <template v-slot:activator="{ on }">
            <v-btn v-on="on"
                   icon
                   @click="show = true">
              <span class="material-icons">settings</span>
            </v-btn>
          </template>
          <span>Customize Columns</span>
        </v-tooltip>
      </template>
      <template v-slot:footer
                v-if="!hideFooter">
        <mrc_ess_paginations v-model="options"
                             @change="turnPage"></mrc_ess_paginations>
      </template>
    </v-data-table>
    <!-- tools -->
    <template>
      <v-dialog v-model="show"
                width="50%"
                scrollable
                max-width="500">
        <v-card>
          <v-card-title class="primary secondary--text pa-1">
            Configuration
            <v-spacer></v-spacer>
            <v-btn icon
                   class="secondary--text"
                   @click.stop="show = false">
              <v-icon>mdi-close-circle-outline</v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text>
            <draggable v-model="dragArr"
                       draggable=".drag"
                       v-bind="{animation:1000}"
                       :list="curHeaders">
              <v-sheet class="d-flex flex-grow-1 pa-0 align-center"
                       :class="canDrag(item.value)?'drag':''"
                       v-for="item in curHeaders"
                       :key="item.value">
                <v-hover v-slot:default="{ hover }"
                         open-delay="100"
                         :style="{cursor:canDrag(item.value)?'grab':'not-allowed'}"
                         class="mt-2">
                  <v-card class="white d-flex flex-row justify-start flex-grow-1 pt-1 pb-1 pr-3 pl-0 align-center"
                          :elevation="hover&&canDrag(item.value) ? 8 : 1"
                          :color="canDrag(item.value)?'white':'grey lighten-3'">
                    <!-- <svg-icon size="36"
                              fill="grey"
                              iconClass="dragvertical" /> -->
                    <v-checkbox color="primary"
                                class="mt-0 pt-0"
                                @change="onSelect"
                                :disabled="!canDrag(item.value)"
                                v-model="item.mrcSelected"
                                :label="canDrag(item.value)?item.text:item.value"
                                hide-details></v-checkbox>
                  </v-card>
                </v-hover>
              </v-sheet>
            </draggable>
          </v-card-text>
          <!-- <CardActions saveBtn @save="toSave" @cancel="show = false" /> -->
        </v-card>
      </v-dialog>
    </template>
  </div>
</template>

<script> 
import draggable from "vuedraggable";
export default {
  components: { draggable },
  props: {
    headers: { default: () => [], type: [Array] },
    itemKey: { default: "uuid", type: String },
    showSelect: { default: false, type: Boolean },
    items: { default: () => [], type: Array },
    hideDefaultHeader: {
      type: Boolean,
      default: false
    },
    paginationOptions: {
      type: Object
    },
    hideAddBtn: {
      type: Boolean,
      default: false
    },
    addBtnList: {
      type: Array
    },
    pageName: {
      type: String
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    slotKey: {
      type: Array,
      default: () => ['actions']
    },
    hideFooter: {
      type: Boolean,
      default: false
    },
    search: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      page: 1,
      show: false,
      refresh: Math.random() * 10000000,
      selected: [],
      dragArr: [],
      cannotDrag: ["color", "action"],
      options: {
        pagesize: 10,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: 0, //总条数
        totalpage: 0, //总页数
        selectlist: [1, 5, 10, 15, 20],
        pagetext: this.$t('label_Each_page_shows')
      },
      tableHeight: ''
    };
  },
  computed: {
    curHeaders () {
      this.headers.forEach(item => {
        item["mrcSelected"] = true;
      });
      return this.headers;
    },
    showHeaders () {
      if (this.refresh) {
        return this.curHeaders.filter(ele => ele.mrcSelected === true);
      }
    }
  },
  created () { },
  watch: {
    paginationOptions: {
      handler (val) {
        Object.assign(this.options, val)
      },
      deep: true
    },
    search (v) {
      console.log(v, this.desserts);
    }
  },
  mounted () {
    Object.assign(this.options, this.paginationOptions)
    this.tableHeight = window.document.documentElement.clientWidth * 0.35
  },

  methods: {
    canDrag (value) {
      if (this.cannotDrag.indexOf(value) != -1) {
        return false;
      } else {
        return true;
      }
    },
    onSelect: function (val) {
      this.refresh = Math.random() * 10000000;
    },
    pageCount (num) {
      // console.log(num, 'pageCount')
    },
    turnPage (options) {
      this.$store.commit('setCpage', options.cpage)
      this.$store.commit('setPagesize', options.pagesize)
      this.$emit('turnPage', options)
    },
    handleAdd () {
      this.$emit('handleAdd')
    },
    clickRow (val) {
      this.$emit('click:row', val)
    }
  },
};
</script>

<style scoped>
/deep/ .v-data-table-header th {
  font-family: Noto Sans CJK TC;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.38);
}
.tableBoxComponent /deep/ .v-data-table__wrapper td {
  font-family: Noto Sans CJK TC;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #383838;
}
.tableBoxComponent /deep/ .theme--light.v-data-table thead tr th {
  color: rgba(0, 0, 0, 0.38);
}
</style>
