<template>
  <mrc_ess_autoComplete :item-key='itemKey'
                        :staffUuid='staffUuid'
                        :listItem='listItem'
                        :multiple='multiple'
                        :item-value='itemValue'
                        :disabled='disabled'
                        :errMsg='errMsg'
                        :dropDownList='dropDownList'
                        :filter='filter'
                        :maxHeight='maxHeight'
                        :placeholder='placeholder?placeholder:$t("label_company")'
                        @changeStaff='changeStaff'
                        @changeStaffList='changeStaffList'
                        :label='label?label:$t("label_department")'>
    <template #default='{item}'>
      <span :class="multiple?'multiple':'singleChoice'">
        <span style="font-size: 16px;color: rgba(0, 0, 0, 0.87);">{{item[itemKey]}}</span>
        <span style="font-size: 14px;color: rgba(0, 0, 0, 0.38);">({{item.org_level_code}})</span>
      </span>
    </template>
    <template #label>
      <slot name='label'></slot>
    </template>
  </mrc_ess_autoComplete>
</template>

<script> 
/**
 *   选单位组件
 */
import { getBusinessList } from '@/service/api/prompt.js'
export default {
  components: {},
  props: {
    errMsg: { //格式必填项验证
    },
    maxHeight: {  //列表总高度
      type: String,
      default: '300'
    },
    listItem: {  //列表每一项高度
      type: String,
      default: '48'
    },
    itemKey: {
      type: String,
      default: 'view_fields'
    },
    itemValue: {
      type: String,
      default: 'value_field'
    },
    label: {
      type: String,
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: { //多选or单选 默认单选
      type: Boolean,
      default: false
    },
    staffUuid: { //uuid
      type: String
    },
    filter: { //过滤条件 默认为空
      type: String,
      default: ''
    },
    // dropDownList: { //请求列表函数
    //   type: Array,
    //   default: () => []
    // },
    placeholder: {
      type: String
    },
  },
  model: {
    prop: 'staffUuid',
    event: 'changeStaff'
  },
  data () {
    return {
      dropDownList: []
    };
  },
  computed: {
  },
  created () { },
  watch: {
  },
  async mounted () {
    const res = await getBusinessList({ prompt_name: 'levelUuid_deptUuid', org_level_uuid: this.$store.state.currentCompany })
    if (res.code == 200) {
      this.dropDownList = res.data
    }
  },
  methods: {
    changeStaffList (v) {
      this.$emit('changeStaffList', v)
    },
    changeStaff (v) {
      this.$emit('changeStaff', v)
    }
  },
};
</script>

<style scoped>
.multiple {
  position: relative;
  left: -15px;
  top: -3px;
}
.singleChoice {
  margin-left: 8px;
}
</style>
