<template>
  <transition name="fade">
    <v-overlay :value="dialog"
               :dark='false'
               style="z-index:999999"
               @click="action(false)">
      <v-card width="300">
        <v-card-title class="headline error"
                      color="error"
                      primary-title>
          <v-icon color="yellow accent-4">mdi-alert-outline</v-icon>{{title || $t('message_tips')}}
        </v-card-title>

        <v-card-text class="pa-10">{{content}}?</v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey lighten-1"
                 class="text-capitalize"
                 text
                 @click="action(false)">{{cancelText || $t('label_no')}}</v-btn>
          <v-btn color="error"
                 class="text-capitalize"
                 text
                 @click="action(true)">{{successText || $t('label_is')}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-overlay>

  </transition>

</template>

<script>
export default {
  name: 'myConfirm',
  components: {},
  data () {
    return {
      loading: false,
      dialog: false,
      content: '确认内容',
      title: '',
      promiseStatus: null,
      successText: '',
      cancelText: ''
    };
  },
  created () { },
  mounted () {
  },
  methods: {
    confirm (fn) {
      this.dialog = true
      return new Promise((resolve, reject) => {
        this.promiseStatus = { resolve, reject }
      })
    },
    action (status) {
      if (status) {
        this.promiseStatus && this.promiseStatus.resolve()
        this.hide()
      } else {
        this.promiseStatus && this.promiseStatus.reject()
        this.hide()
      }
    },
    hide () {
      this.dialog = false;
    }
  },
  computed: {},
  watch: {
  },
};
</script>

<style scoped>
.confirm {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 325px;
}
</style>
