//国际化
import http from '../httpConfig'

// 下载模板 
export const downTemplate = (params) => {
  return new Promise((resolve, reject) => {
    http.post('/api/language/download-module/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

// 基本信息导入
export const uploadHint = (params) => {
  return new Promise((resolve, reject) => {
    http.uploadFile('/api/language/upload-language-master/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

