<template>
  <v-card>
    <v-card-title>
      <mrc_ess_searchInput :isShow='true'
                           v-model="search"
                           @keydown='keydown'
                           @change='change'
                           style="width:100%"
                           :appendShow='false'></mrc_ess_searchInput>
    </v-card-title>
    <v-card-text>
      <div class="top"
           :style="{'max-height':maxHeight+'px',overflowX: 'hidden'}">
        <!--  item-key='staff_property_master_uuid' -->
        <v-treeview dense
                    :search="search"
                    :items="allList"
                    selection-type="independent"
                    :return-object='true'
                    :open-all='true'
                    item-disabled="disabled"
                    @input="treeInput"
                    :item-text='itemText'
                    :item-key='itemKey'>

          <template v-slot:prepend="{ open }">
            <v-icon>
              {{open ? 'mdi-folder-open' : 'mdi-folder' }}
            </v-icon>
          </template>

          <template v-slot:label="{ item }">
            <span style="position:relative;display: inline-block;height: 40px;line-height: 40px">{{item[itemText]}}
              <span @click.stop="getDetail(item)"
                    style="position: absolute;right: 0;top: 0;width: 100%;height: 100%;background-color: darkred;opacity: 0"></span>
            </span>

          </template>

          <template v-slot:append="{ item }">
            <!-- <span @click.stop="getDetail(item)"
                  style="position: absolute;right: 0;top: 0;width: 80%;height: 100%;background-color: darkred;opacity: 0"></span> -->
            <v-icon class="material-icons mr-3"
                    v-if="item.active"
                    style="color:rgb(19, 166, 113)">
              check_circle
            </v-icon>
          </template>

        </v-treeview>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
/**
 * 
 * 
 * 
 * 
 * @detailStaff 获取员工的详情数据
 * @selectAll  选择列表中的全部员工
 * @getDetail 
 */
export default {
  components: {},
  props: {
    allList: { //树结构
      type: Array,
      default: () => []
    },
    maxHeight: {
      type: String,
      default: '400'
    },
    itemText: {
      type: String,
    },
    itemKey: {
      type: String,
    }
  },
  model: {
    prop: 'allList',
    event: 'changeAllList'
  },
  data () {
    return {
      search: null, //搜索值
      caseSensitive: false, //区分大小写
      open: [1, 2],
      // selectAllStatus: false,//全选状态 默认不全选
      timeout: null,
      selection: [],
      allParentNodes: false,
      trueStatus: []
    };
  },
  computed: {
  },
  created () { },
  watch: {

  },
  mounted () {

  },

  methods: {
    keydown (v) {
    },
    change (v) {
    },
    getDetail (item) {
      if (this.timeout !== null) clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        let res = this.allList
        this.changeActive(res, item.uuid)
        this.changeParents(res)
        this.$emit('changeAllList', res)
      }, 500);
    },
    treeInput (val) {
      console.log(val);
    },
    activeUpdate (val) {
      console.log(val);
    },
    changeActive (arr, uuid) { //改变当前点击节点的状态
      arr.map((item, index) => {
        if (item.uuid == uuid) {
          this.$set(arr, index, { ...item, active: !item.active })
          if (item.children && item.children.length > 0) {
            this.recursionActive(item.children, !item.active)
          }
        }
        else {
          if (item.children && item.children.length > 0) {
            this.changeActive(item.children, uuid)
          }
        }
      })
    },
    recursionActive (arr, val) { //递归将子节点变为 父节点的状态
      arr.map((item, index) => {
        this.$set(arr, index, { ...item, active: val })
        if (item.children && item.children.length > 0) {
          this.recursionActive(item.children, val)
        }
      })
    },
    changeParents (arr) { //递归根据子节点改变父节点状态
      arr.map((item, index) => {
        if (item.children && item.children.length > 0) {
          // if (item.children.every(item1 => {
          //   return item1.active == true
          // })) {
          //   this.$set(arr, index, { ...item, active: true })
          //   this.recursionActive(item.children, true)
          // }
          this.trueStatus = []
          this.checkStatus(item.children)
          if (this.trueStatus.every(item => {
            return item == true
          })) {
            this.$set(arr, index, { ...item, active: true })
            this.recursionActive(item.children, true)
          }
          else {
            this.$set(arr, index, { ...item, active: false })
            this.changeParents(item.children)
          }
        }
      })
    },
    checkStatus (arr) {  // 递归获取子节点全部数据  我太难了
      arr.map(item => {
        if (!item.children || item.children == 0) {
          this.trueStatus.push(item.active)
        }
        if (item.children && item.children.length > 0) {
          this.checkStatus(item.children)
        }
      })
    },
    recursionChengji (arr, otr) {  //递归加层级
      arr.map((item, index) => {
        this.$set(arr, index, { ...item, 'otr': otr + item[this.itemText] })
        if (item.children && item.children.length > 0) {
          let od = otr + item[this.itemText] + '>'
          this.recursionChengji(item.children, od)
        }
      })
    }
  },
};
</script>

<style scoped>
.spans {
  max-width: 30vw;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.top {
  border: 2px solid #ececec;
  overflow-y: auto;
}
.footer {
  min-height: 350px;
  border: 2px solid #ececec;
  box-shadow: none;
}
.footer_box {
  max-height: 300px;
  overflow-y: auto;
}
.staffactive {
  background-color: #e5eeff;
}
</style>
