<template>
  <mrc_ess_autoComplete :item-key='itemKey'
                        :staffUuid='staffUuid'
                        :listItem='listItem'
                        :multiple='multiple'
                        :item-value='itemValue'
                        :disabled='disabled'
                        :dropDownList='dropDownList'
                        @changeStaffList='changeStaffList'
                        @changeStaff='changeStaff'
                        :errMsg='errMsg'
                        :filter='filter'
                        :maxHeight='maxHeight'
                        :placeholder='placeholder'
                        :label='label'>
    <template #default='{item}'>
      <div :class="multiple?'multiple':'singleChoice'">
        <div style="font-size:16px;color:rgba(0, 0, 0, 0.87);font-size:16px;"
             class="mb-1">
          <span>{{item[itemKey]}}</span>
          <span>({{item[itemKey]}})</span>
        </div>
        <div class="positionItem"
             style="max-width:502px;font-size: 16px;color: rgba(0, 0, 0, 0.38);font-size:14px;overflow:hidden;text-overflow:ellipsis;white-space: nowrap;">
          {{item[itemKey]}}++++++++++++++
        </div>
      </div>
    </template>
    <template #label>
      <slot name='label'></slot>
    </template>
  </mrc_ess_autoComplete>
</template>

<script>
/** 
 *   选岗位（含有岗位等级信息）组件
 */
import { getPositionList } from '@/service/api/chooseStaff.js'
export default {
  components: {},
  props: {
    errMsg: { //格式必填项验证
    },
    maxHeight: {  //列表总高度
      type: String,
      default: '300'
    },
    listItem: {  //列表每一项高度
      type: String,
      default: '64'
    },
    itemKey: {
      type: String,
      default: 'name'
    },
    itemValue: {
      type: String,
      default: 'uuid'
    },
    label: {
      type: String,
      default: '岗位及等级*'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: { //多选or单选 默认单选
      type: Boolean,
      default: false
    },
    staffUuid: { //uuid
      type: String
    },
    filter: { //过滤条件 默认为空
      type: String,
      default: ''
    },
    // dropDownList: { //请求列表函数
    //   type: Array,
    //   default: () => []
    // },
    placeholder: {
      type: String,
      default: '岗位等级'
    }
  },
  model: {
    prop: 'staffUuid',
    event: 'changeStaff'
  },
  data () {
    return {
      dropDownList: []
    };
  },
  computed: {},
  created () { },
  watch: {
  },
  async mounted () {
    const res = await getPositionList({
      uuid: this.$store.state.currentCompany
    })
    this.dropDownList = res.data
  },
  methods: {
    changeStaffList (v) {
      this.$emit('changeStaffList', v)
    },
    changeStaff (v) {
      this.$emit('changeStaff', v)
    }
  },
};
</script>

<style scoped>
.multiple {
  padding-top: 20px;
  height: 100%;
  flex: 1;
  /* left: -13px;
  position: relative; */
}
.singleChoice {
  margin-left: 8px;
}
</style>
