<template>
  <mrc_ess_autoComplete :item-key='itemKey'
                        :staffUuid='staffUuid'
                        :listItem='listItem'
                        :multiple='multiple'
                        :item-value='itemValue'
                        :disabled='disabled'
                        :errMsg='errMsg'
                        :dropDownList='dropDownList'
                        :filter='filter'
                        @changeStaff='changeStaff'
                        @changeStaffList='changeStaffList'
                        :maxHeight='maxHeight'
                        :placeholder="placeholder?placeholder:$t('label_company')"
                        :label="label?label:$t('label_Use_the_unit')">
    <template #default='{item}'>
      <span :class="multiple?'multiple':'singleChoice'">
        <span style="font-size: 16px;color: rgba(0, 0, 0, 0.87);">{{item[itemKey]}}</span>
        <span style="font-size: 14px;color: rgba(0, 0, 0, 0.38);">({{item.org_level_code}})</span>
      </span>
    </template>
    <template #label>
      <slot name='label'></slot>
    </template>
  </mrc_ess_autoComplete>
</template>

<script>
/**
 *   选单位组件
 */
export default {
  components: {},
  props: {
    errMsg: { //格式必填项验证
    },
    maxHeight: {  //列表总高度
      type: String,
      default: '300'
    },
    listItem: {  //列表每一项高度
      type: String,
      default: '48'
    },
    itemKey: {
      type: String,
      default: 'org_level_name'
    },
    itemValue: {
      type: String,
      default: 'uuid'
    },
    label: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: { //多选or单选 默认单选
      type: Boolean,
      default: false
    },
    staffUuid: { //uuid
      type: String
    },
    filter: { //过滤条件 默认为空
      type: String,
      default: ''
    },
    // dropDownList: { //请求列表函数
    //   type: Array,
    //   default: () => []
    // },
    placeholder: {
      type: String
    },
  },
  model: {
    prop: 'staffUuid',
    event: 'changeStaff'
  },
  data () {
    return {
      dropDownList: []
    };
  },
  computed: {},
  created () { },
  watch: {
  },
  mounted () {
    if (this.$store.state.topcompanylist.length > 0) this.dropDownList = this.$store.state.topcompanylist
  },
  methods: {
    changeStaffList (v) {
      this.$emit('changeStaffList', v)
    },
    changeStaff (v) {
      this.$emit('changeStaff', v)
    }
  },
};
</script>

<style scoped>
.multiple {
  position: relative;
  left: -15px;
  top: -3px;
}
.singleChoice {
  margin-left: 8px;
}
</style>
