export default {
  install (Vue) {
    // 自定义按钮指令
    // Vue.directive('btnAuth', {
    //   inserted (el, binding) {
    //     const btnPermission = JSON.parse(localStorage.getItem('btnPermission')) || []
    //     let show = false
    //     show = binding.value.some(item => btnPermission.includes(item))

    //     if (show) {
    //       el.style.display = 'block'
    //     } else {
    //       el.style.display = 'none'
    //     }
    //   }
    // })
    Vue.directive('btnAuth', (el, binding) => {
      const btnPermission = JSON.parse(localStorage.getItem('btnPermission')) || []
      let show = false
      show = binding.value.some(item => btnPermission.includes(item))

      if (show) {
        el.style.display = 'block'
      } else {
        el.style.display = 'none'
      }
    })
  }
}