<template>
  <v-snackbar v-model="snackbar"
              :timeout="timeout"
              :color='color'>
    <span style="color:#fff">
      {{ text }}
    </span>
  </v-snackbar>
</template>

<script>
export default {
  components: {},
  props: {},
  data () {
    return {
      snackbar: false,
      text: '',
      timeout: 2000,
      color: 'rgb(19, 166, 113)'  // 不接受black的css
    };
  },
  computed: {},
  created () { },
  watch: {},
  mounted () { },

  methods: {
    changeSnackbar () {
      this.snackbar = true
    }
  },
};
</script>

