import { btnPermissionList } from '@/service/api/btnPermission'
export default {
  data () {
    return {

    }
  },
  methods: {
    async getBtnPermissionList () {
      const res = await btnPermissionList({
        function_uuid: this.$store.state.menuFunctionUuid
      })
      this.$store.commit('setBtnPermission', res.data ? res.data : [])
    },
    hasBtn (name) {
      return this.$store.state.btnPermission.includes(name)
    }
  }
}