<template>
  <div>
    <v-menu offset-y
            v-model="showMenu"
            z-index='300'
            :disabled='disabled'
            :close-on-content-click='!multiple'>
      <template v-slot:activator="{ on, attrs }">
        <div>
          <!-- 单选 -->
          <div v-if="!multiple"
               style="width:100%">
            <div style="font-size:13px;"
                 class="pl-2">
              {{label}}
              <slot name="label"></slot>
            </div>
            <div class="d-flex justify-space-between align-center pl-2"
                 v-bind="attrs"
                 v-on="on"
                 :style="{width:'100%','background-color':disabled?'':'#F5F5F5','border-bottom':'1px solid rgba(0, 0, 0, 0.38)','border-radius':' 4px 4px 0px 0px'}">
              <div class="d-flex align-center">
                <span style="font-size: 16px;color: rgba(0, 0, 0, 0.87);">{{INT(viewList[itemKey])}}</span>
              </div>
              <input type="text"
                     @input='input'
                     :readonly='readonly'
                     :placeholder="viewList[itemKey]?'':placeholder"
                     v-model="search"
                     :disabled='disabled'
                     style="border:none;outline:none;height:44px;flex:1;">
              <div style="line-height:44px"
                   v-if="!disabled">
                <v-icon class="material-icons">{{showMenu?'expand_more':'expand_less'}} </v-icon>
              </div>
            </div>
          </div>
          <!-- 多选 -->
          <div v-else
               style="width:100%">
            <div style="font-size:13px;"
                 class="pl-2">
              {{label}}
              <slot name="label"></slot>
            </div>
            <div class="d-flex pl-2"
                 v-bind="attrs"
                 v-on="on"
                 :style="{width:'100%','background-color':disabled?'':'#F5F5F5','border-bottom':'1px solid rgba(0, 0, 0, 0.38)','border-radius':' 4px 4px 0px 0px'}">
              <div class="d-flex align-center">
                <v-chip class="mr-2"
                        @click:close='closeChip(item)'
                        v-for="item in viewList"
                        :key="item[itemValue]"
                        :close='!disabled'>
                  <span style="font-size: 16px;color: rgba(0, 0, 0, 0.87)">
                    {{INT(item[itemKey])}}
                  </span>
                </v-chip>
              </div>
              <div class="d-flex justify-space-between align-center"
                   style="width:100%">
                <input type="text"
                       :readonly='readonly'
                       :placeholder="viewList.length>0?'':placeholder"
                       :disabled='disabled'
                       @input='input'
                       v-model="search"
                       style="border:none;outline:none;height:44px;width:100%;">
                <div style="line-height:44px"
                     v-if="!disabled">
                  <v-icon class="material-icons">{{showMenu?'expand_more':'expand_less'}} </v-icon>
                </div>
              </div>
            </div>
          </div>
          <div v-if="errMsg&&errMsg.length>0"
               style="color: #ff5252;font-size:12px;">
            {{errMsg[0]}}
          </div>
        </div>
      </template>
      <!-- ----------------------------下拉列表 -------------------------------------->
      <v-list :style="{'max-height':maxHeight+'px'}">
        <v-list-item v-for="(item, index) in selectList"
                     :style="{background:item.checked&&multiple? '#E5EEFF':'#ffffff'}"
                     :key="index"
                     @click="selectItem(item)"
                     class="pl-0">
          <v-list-item-title>
            <!-- 单选 -->
            <div v-if="!multiple"
                 :style="{height:listItem+'px',width:'100%'}"
                 class="d-flex align-center">
              <slot :item="item">
                <span style="font-size: 16px;color: rgba(0, 0, 0, 0.87)">{{INT(item[itemKey])}}</span>
              </slot>
            </div>
            <!-- 多选 -->
            <div v-if="multiple"
                 :style="{height:listItem+'px',width:'100%'}"
                 class="d-flex align-center">
              <div>
                <v-checkbox class="mx-2"
                            v-model="item.checked"
                            @change="changeChecked(item)"></v-checkbox>
              </div>
              <slot :item="item">
                <div style="height:100%;flex:1;left: -17px;position: relative;"
                     class="pt-5">
                  <div style="font-size:16px;color:rgba(0, 0, 0, 0.87);font-size:16px;"
                       class="mb-1">{{INT(item[itemKey])}}</div>
                  <div class="positionItem"
                       style="max-width:502px;font-size: 16px;
                       color: rgba(0, 0, 0, 0.38);font-size:14px;
                       overflow:hidden;text-overflow:ellipsis;
                       white-space: nowrap;">
                    {{INT(item[itemKey])}}
                  </div>
                </div>
              </slot>
            </div>
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>

<script>
/**
 * 通用 AutoComplete 组件
 * 
 */
import { getBusinessList } from '@/service/api/prompt.js'
export default {
  components: {},
  props: {
    maxHeight: {  //列表总高度
      type: String,
      default: '300'
    },
    listItem: {  //列表每一项高度
      type: String,
      default: '64'
    },
    itemKey: {
      type: String,
      default: 'name'
    },
    itemValue: {
      type: String,
      default: 'uuid'
    },
    label: {
      type: String,
      default: 'Staff'
    },
    readonly: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: { //多选or单选 默认单选
      type: Boolean,
      default: false
    },
    staffUuid: { //uuid
      type: String
    },
    filter: { //过滤条件 默认为空
      type: String,
      default: ''
    },
    dropDownList: { //下拉列表
      type: Array,
      default: () => []
    },
    placeholder: {
      type: String,
      default: '员工姓名'
    },
    errMsg: { //验证
    },
  },
  model: {
    prop: 'staffUuid',
    event: 'changeStaff'
  },
  data () {
    return {
      showMenu: false,
      allList: [], //所有员工列表
      selectList: [], //过滤后的员工列表
      viewList: {}, //用户选中的员工  数组or对象
      search: '', //搜索字段
    };
  },
  computed: {},
  async created () {
  },
  watch: {
    staffUuid (v) {
      // console.log(v);
      this.getStaffInfo()
    },
    dropDownList: {
      handler (val) {
        this.getSelectList()
        this.selectList = this.allList
        this.getStaffInfo()
      }, deep: true
    }
  },
  async mounted () {
    await this.getSelectList()
    this.selectList = this.allList
    this.getStaffInfo()
  },
  methods: {
    selectItem (val) {
      if (!this.multiple) {//单选
        this.$emit('changeStaff', val[this.itemValue])
        this.$emit('changeStaffList', val)
        this.search = ''
      }
    },
    closeChip (val) {
      // console.log(val);
      this.changeViewList(val)
    },
    changeViewList (val) {
      this.viewList = this.viewList.filter(item => {
        return item[this.itemValue] != val[this.itemValue]
      })
      this.$emit('changeStaffList', this.viewList)
      let res = this.viewList.map(item => {
        return item[this.itemValue]
      })
      this.$emit('changeStaff', res.join())
    },
    changeChecked (val) {
      if (val.checked) {
        this.viewList.push(val)
        this.$emit('changeStaffList', this.viewList)
        let res = this.viewList
        res = res.map(item => {
          return item[this.itemValue]
        })
        this.$emit('changeStaff', res.join())
      }
      else {
        if (this.viewList.length > 0) {
          this.changeViewList(val)
        }
        else {
          this.viewList = []
          this.$emit('changeStaffList', this.viewList)
          this.$emit('changeStaff', '')
        }
      }
    },
    input () { //搜索
      if (this.search != '') {
        let arr = []
        this.allList.map(item => {
          let isNo = false
          if (this.INT(item[this.itemKey]).includes(this.search)) {
            let isNo = true
            arr.push(item)
          }
        })
        this.selectList = arr
      }
      else {
        this.selectList = this.allList
      }
      this.showMenu = true
    },
    async getSelectList () { //获取列表
      if (!this.dropDownList.length > 0) return
      if (!this.allList.length > 0) {
        this.allList = this.dropDownList.map(item => {
          item.checked = false
          return item
        })
      }
    },
    getStaffInfo () {
      if (!this.allList.length > 0) this.getSelectList()
      if (this.multiple) { //多选
        if (!this.staffUuid) {
          this.selectList = this.selectList.map(item => {
            item.checked = false
            return item
          })
          return this.viewList = []
        }
        const res = this.staffUuid.split(',')
        this.viewList = []
        this.allList.map(item => {
          item.checked = false
          res.map(item1 => {
            if (item1 == item[this.itemValue]) {
              item.checked = true
              this.viewList.push(item)
            }
          })
        })
        this.$emit('changeStaffList', this.viewList)
      }
      else {//单选
        if (!this.staffUuid) return this.viewList = {}
        this.viewList = {}
        this.allList.map(item => {
          if (this.staffUuid == item[this.itemValue]) {
            this.viewList = item
          }
        })
        this.$emit('changeStaffList', this.viewList)
      }
    },

  },
};
</script>

<style scoped>
</style>
