<template>
  <span class="d-flex">
    <span class="d-flex"
          style="width:40px;height:40px">
      <v-img style="border-radius:50%"
             :src="`https://picsum.photos/500/300?image=${5 * 5 + 10}`"
             :lazy-src="`https://picsum.photos/10/6?image=${5 * 5 + 10}`"
             class="grey lighten-2"></v-img>
    </span>
    <span class="ml-4">
      <span style="display: block;font-size: 14px;color:color: #383838;">
        {{list.christian_name?list.christian_name+' \xa0/\xa0 ':''}}{{list.show_name?INT(list.show_name):list.show_name}}
      </span>
      <span style="display: block;color: rgba(0, 0, 0, 0.38);font-size:12px;"
            v-if="isShow">
        <span class="mr-3 spans"
              v-for="(item,index) in post_and_grade"
              :key="item+index">
          {{item+(index==post_and_grade.length-1?'':'>')}}
        </span>
        <span class="spans"
              v-for="(item,index) in client_and_org"
              :key="item+index">
          {{item+(index==client_and_org.length-1?'':'>')}}
        </span>
      </span>
    </span>
  </span>
</template> 

<script>
export default {
  components: {},
  props: {
    list: {
      type: Object,
      default: () => { return {} }
    },
    isShow: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {

    };
  },
  computed: {
    post_and_grade () {
      if (this.list.post_and_grade) {
        return this.list.post_and_grade.indexOf(',') != -1 ? String(this.list.post_and_grade).split() : [this.list.post_and_grade]
      }
      else {
        return []
      }
    },
    client_and_org () {
      if (this.list.client_and_org) {
        return this.list.client_and_org.indexOf(',') != -1 ? String(this.list.client_and_org).split() : [this.list.client_and_org]
      }
      else {
        return []
      }
    }
  },
  created () { },
  watch: {},
  mounted () { },

  methods: {

  },
};
</script>

<style scoped>
.spans {
  max-width: 30vw;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
