export default {
  computed: {

    requiredRule () {
      return [
        {
          rule: 'required',
          msg: this.$t('verify_Required')
        }
      ]
    },
    urlRule () {
      return [
        {
          rule: 'url',
          msg: this.$t('verify_The_URL_is_in_the_wrong_format')
        }
      ]
    },
    integerRule () {
      return [
        {
          rule: 'integer',
          msg: this.$t('verify_Please_enter_the_integer_value.')
        }
      ]
    },
    emailRule () {
      return [
        {
          rule: 'email',
          msg: this.$t('verify_The_mailbox_format_is_wrong')
        }
      ]
    },
    numericRule () {
      return [
        {
          rule: 'numeric',
          msg: this.$t('verify_Only_numbers_can_be_entered')
        }
      ]
    },
    nocharacter () {
      return [
        {
          rule: 'nocharacter',
          msg: this.$t('verify_You_cannot_enter_illegal_characters')
        }
      ]
    }
  },
  methods: {

    maxLength (num) {
      return [
        {
          rule: 'maxLength',
          msg: this.$t('verify_The_most_input') + `${num}` + this.$t('verify_Character')
        }
      ]
    },
    between (str) {
      return [
        {
          rule: 'between',
          msg: this.$t('verify_Please_enter') + `${str}` + this.$t('verify_digital')
        }
      ]
    },
    sameAsKey (str) {
      return [
        {
          rule: 'sameAsKey',
          msg: this.$t('verify_Entered_twice') + `${str}` + this.$t('verify_Inconsistent')
        }
      ]
    },
    // 校验方法
    isChina (val) {
      const reg = new RegExp("[\\u4E00-\\u9FFF]+", "g")
      return !reg.test(val)
    },
    illegal (val) {
      return !/[#<>{}()|/\+-=\[\]]/.test(val)
    },

    /**
     * 
     * @param {对象名字} objName 
     * @param {属性名字} name 
     * @param {校验的类型 和 对应的提示消息} arr 
     */
    $errors (objName = null, name, arr) {
      // 校验参数可能是嵌套数据  嵌套数据那传过来的就是对象  此处只做一层嵌套
      const errors = []
      const dirty = objName ? !this.$v[objName][name].$dirty : !this.$v[name].$dirty
      if (dirty) return errors
      arr.forEach(item => {
        objName
          ?
          !this.$v[objName][name][item.rule] && errors.push(item.msg)
          :
          !this.$v[name][item.rule] && errors.push(item.msg)
      })
      return errors
    },
  }
}