// 功能模块管理 
import http from '../httpConfig'
// 高级参照业务列表
export const getBusinessList = (prompt_name) => {
  const params = {
    prompt_name,
    "~table~": 'mrbase_sys_prompt'
  }
  return new Promise((resolve, reject) => {
    http.post('/api/prompt/business/findPrompt/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}
