/**
 * 删除后端传过来的多余的数据
 * "created_by": "",
 *"created_utc_datetime":
 *"updated_by": "",
 *"updated_utc_datetime"
 *
 */
import store from '@/store'
import http from '@/service/httpConfig.js'
import axios from 'axios'
import azureUpload from 'vue-azure-blob-upload'
import Vue from 'vue'
import { get_list } from '@/service/api/func_module'
import {
  addFile, addResultFile, deleteFile, deleteResultFile
} from '@/service/api/fileManagements.js'
import moment from 'moment'

const $deletefield = function (arr, obj) { //传对象  数组要传null $deletefield(null,obj)
  if (arr) {
    if (arr.length > 0) {
      return arr.map(item => {
        delete item.created_by
        delete item.created_utc_datetime
        delete item.updated_by
        delete item.updated_utc_datetime
        return item
      })
    } else {
      return []
    }
  }
  if (Object.keys(obj).length > 0) {
    delete obj['created_by']
    delete obj['created_utc_datetime']
    delete obj['updated_by']
    delete obj['updated_utc_datetime']
    return obj
  } else {
    return {}
  }
}

//格式化日期
const $moment = (val, val2 = 'YYYY-MM-DD') => {
  return moment(val).format(val2)
}

const getProductUuid = async () => { //获取ProductUuid 
  const res = await get_list()
  if (res.code == 200) {
    store.commit('set_product_uuid', res.data[0].uuid)
  }
  else {
    store.commit('newsTips', '未获取到产品信息')
  }
}

function azureUploadFn (url, f, uuid) {
  return new Promise((resolve, reject) => {
    azureUpload(console.log.bind(console), axios.create())({
      baseUrl: url,
      sasToken: '?sv=2019-12-12&ss=bfqt&srt=co&sp=rwdlacupx&se=2020-09-18T11:56:39Z&st=2020-08-18T03:56:39Z&spr=https,http&sig=T%2FFfmd4JSaxFM%2FdPQGGxggUnBOazPfcUcy9Umit6sdg%3D',
      file: f,
      progress: (progress) => {
        // console.log(progress);
      },
      complete: (res) => {
        addResultFile({
          uuid: uuid,
          result: 1
        }).then(res => {
          resolve(res)
        })

      },
      error: (error) => {
        addResultFile({
          uuid: uuid,
          result: 2
        }).then(err => {
          reject(err)
        })
      },
      blockSize: f.size,
    })
  })
}

//上传
const $uploadFiles = async (file, options) => {
  if (!file) return
  let f = file[0]
  if (!store.state.product_uuid) {
    await getProductUuid()
  }
  let res = await addFile({
    ...options,
    org_level_uuid: store.state.currentCompany,
    function_uuid: store.state.menuFunctionUuid,
    product_uuid: store.state.product_uuid,
    attachment_title: f.name,
    attachment_size: f.size
  })
  if (res.code == 200) {
    // if (res.data.size > f.size) {
    //   res = res.data
    // }
    // else {
    //   return store.commit('newsTips', '内存不足')
    // }
    res = res.data
  }
  else return

  let url = res.attachment_domain + '/' + res.attachment_container + '/' + f.name
  // let url = 'https://mrcbase.blob.core.windows.net/10004-container/' + f.name
  // let url = 'https://mrcbase.blob.core.windows.net/upload-test/' + f.name
  const res2 = await azureUploadFn(url, f, res.uuid)
  return res2
}





export {
  $deletefield, $moment, $uploadFiles
}

