import axios from 'axios'
import qs from 'qs'
import store from '@/store/index.js'
import baseURL from './baseUrl'
import router from '../router/index'
const http = {}


var instance = axios.create({
  timeout: 10000000000000,
  baseURL,
})
instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// instance.defaults.headers.post['Content-Type'] = 'multipart/form-data';
// 添加请求拦截器
instance.interceptors.request.use(
  function (config) {
    // 请求头添加token
    // if (store.state.token) {
    //   config.headers.token = store.state.token
    //   config.headers.system_language_code = store.state.lang
    //   config.headers.system_client_type = 'web'
    //   config.headers.system_client_version = 'v1'
    // }
    // map.put("language", "mslc1112");
    // store.commit('newsTips', '开始请求')
    return config
  },
  function (error) {
    return Promise.reject(error)
  }
)

// 响应拦截器即异常处理
instance.interceptors.response.use(
  res => {
    if (res.status === 200 && res.data.code != 200) {
      switch (res.data.code) {
        case 400:
          break;
        case 401:
          break;
        case 403:
          break;
        case 500:
          break;
      }

      // 统一配置错误提示
      store.commit('newsTips', res.data.msg)
      //记得复原
      if (res.data.msg && res.data.msg.indexOf('重新') > 0 && res.config.url.indexOf('check-token') <= -1) {
        router.push('/login')
      }
    }

    return res
  },
  err => {
    console.log(err)
    if (err && err.response) {
      switch (err.response.status) {
        case 400: err.message = '请求错误(400)'; break;
        case 401: err.message = '未授权，请重新登录(401)'; break;
        case 403: err.message = '拒绝访问(403)'; break;
        case 404: err.message = '请求出错(404)'; break;
        case 408: err.message = '请求超时(408)'; break;
        case 500: err.message = '服务器错误(500)'; break;
        case 501: err.message = '服务未实现(501)'; break;
        case 502: err.message = '网络错误(502)'; break;
        case 503: err.message = '服务不可用(503)'; break;
        case 504: err.message = '网络超时(504)'; break;
        case 505: err.message = 'HTTP版本不受支持(505)'; break;
        default: err.message = `连接出错(${err.response.status})!`;
      }
    } else {
      err.message = '连接服务器失败!'
    }
    return Promise.reject(err.response)
  }
)

http.get = function (url, options) {
  // data.token = store.state.token
  // data.timestamp = new Date().valueOf()
  return new Promise((resolve, reject) => {
    instance
      .get(url, options)
      .then(response => {
        resolve(response.data)
      })
      .catch(e => {
        reject(e)
      })
  })
}

http.post = function (url, data, options) {
  store.state.token && (data.token = store.state.token)
  // data.system_language_code = 'mslc1112'
  data.system_language_code = store.state.lang
  data.system_client_type = 'web'
  data.system_client_version = 'v1'
  // data.timestamp = new Date().valueOf()
  return new Promise((resolve, reject) => {
    instance
      .post(url, qs.stringify({ "formData": JSON.stringify(data) }), options)
      .then(response => {
        resolve(response.data)
      })
      .catch(e => {
        reject(e)
      })
  })
}

/**
 * 上传文件的请求
 * @param url
 * @returns {AxiosPromise}
 */
http.uploadFile = function (url, data) {
  let config = {
    //请求的接口，在请求的时候，如axios.get(url,config);这里的url会覆盖掉config中的url
    url: url,
    //跨域请求时是否需要使用凭证
    withCredentials: true,
    // 返回数据类型
    responseType: 'json', //default
  };
  return instance.post(url, data, config);
};
export default http
