<template>
  <span>
    <v-btn rounded
           @click="dialogClick"
           height='35'
           color="primary"
           class="text-capitalize"
           dark>{{label}}</v-btn>
    <mrc_ess_myDialog v-model="dialog"
                      @flag='flag'
                      :noPadding='false'
                      :title="$t('label_Upload_CVS')"
                      width='847'
                      :disabled='false'>

      <mrc_ess_myStep :list='stepList'
                      class="bigBoxBox"
                      v-model="stepIndex">
        <template v-slot:1>
          <span class="d-flex ml-8 mt-8"
                style="color:rgb(164, 164, 164);font-size:14px">
            文件
          </span>
          <mrc_ess_importFile class="mr-8 ml-8 mb-8"
                              v-model="file"></mrc_ess_importFile>
          <mrc_ess_submitBtn @success='changeIndex(1)'
                             :successText="$t('label_The_next_step')"
                             @cancel='changeIndex(0)'></mrc_ess_submitBtn>
        </template>
        <template v-slot:2>
          <div v-if="!uploadLoading">
            <div v-if="resStatus"
                 class="pr-8 pl-8 d-flex"
                 style="min-height:350px;width:100%;justify-content:center;align-items:center;font-size:20px">
              <span>导入成功!</span>
            </div>
            <div v-else>
              <div class="pr-8 pl-8 d-flex"
                   v-if="!tableStatus"
                   style="min-height:350px;width:100%;justify-content:center;align-items:center;font-size:20px">
                <span class="d-flex flex-column">
                  <span>导入失败!</span>
                  <span @click="downloadError"
                        class="ml-n5"
                        :style="{'color': $vuetify.theme.defaults.light.primary,'text-decoration': 'underline'}">下载错误信息</span>
                </span>

              </div>
              <!--错误信息 ----------- -->
              <div v-if="tableStatus"
                   class="pr-8 pl-8">
                <!-- <slot name='table'></slot> -->
                <mrc_ess_table :headers='headers'
                               :paginationOptions='options'
                               @turnPage='getlist'
                               :isLoading='loading'
                               :items='desserts'>
                </mrc_ess_table>
              </div>
              <!-- ------- -->
            </div>

            <mrc_ess_submitBtn v-if="resStatus"
                               successText='完成'
                               @success='changeIndex(2)'
                               :cancelShow='false'></mrc_ess_submitBtn>
            <mrc_ess_submitBtn v-if="!resStatus&&!tableStatus"
                               successText='查看错误信息'
                               cancelText='关闭'
                               @success='changeIndex(4)'
                               @cancel='changeIndex(3)'></mrc_ess_submitBtn>
            <mrc_ess_submitBtn v-if="!resStatus&&tableStatus"
                               successText='下载错误信息'
                               cancelText='关闭'
                               @success='changeIndex(5)'
                               @cancel='changeIndex(3)'></mrc_ess_submitBtn>
          </div>
          <div v-else
               class="d-flex"
               style="min-height:350px;width:100%;justify-content:center;align-items:center;">
            <svg id="load"
                 x="0px"
                 y="0px"
                 viewBox="0 0 150 150">
              <circle id="loading-inner"
                      cx="75"
                      cy="75"
                      r="60" />
            </svg>

          </div>
        </template>
      </mrc_ess_myStep>

    </mrc_ess_myDialog>
  </span>
</template>
 
<script>
/** 
 * @findError  查询信息  接受函数   this.fn = getStaffLeaveList
 * @sendRequest 上传请求 发送请求到后端
 * @downloadError 下载错误信息
 */
export default {
  components: {},
  props: {
    label: {
      type: String,
    },
    resStatus: { //请求完成之后的状态
      type: Boolean,
    },
    uploadLoading: { //请求加载状态
      type: Boolean,
      default: true
    },
    fn: { //请求错误信息接口的函数
      type: Function
    }
  },
  data () {
    return {
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      desserts: [],
      dialog: false, //弹窗
      loading: false, //table加载状态
      stepIndex: 1,
      file: null,
      tableStatus: false  //表格显示
    };
  },
  computed: {
    stepList () {
      return [
        {
          name: this.$t('label_Importing_files')
        },
        {
          name: this.$t('label_result')
        },
      ]
    },
    headers () {
      return [
        { text: '存储格', align: 'start', value: 'dimission_reason', sortable: false },
        { text: '内容', value: 'dimission_reason', sortable: false },
        { text: '错误原因', value: 'dimission_reason', sortable: false, },
      ]
    },
  },
  created () { },
  watch: {

  },
  mounted () { },

  methods: {
    dialogClick () {
      this.dialog = true
    },
    changeIndex (v) {
      if (v == 0) {
        this.dialog = false
      }
      else if (v == 1) { //下一步 上传文件至后端
        this.stepIndex = 2
        this.$emit('sendRequest', this.file)
      }
      else if (v == 2) { //导入成功 关闭页面
        this.flag()
      }
      else if (v == 3) { //导入失败 关闭页面
        this.flag()
      }
      else if (v == 4) { //导入失败  请求错误信息列表
        this.findError()
      }
      else if (v == 5) { //导入失败  下载错误信息
        this.downloadError()
      }
    },
    flag () {
      this.dialog = false
      this.tableStatus = false
      if (this.stepIndex == 2) {
        this.stepIndex = 1
      }
    },
    findError () {  //查看错误
      this.$emit('findError')
      setTimeout(() => {
        this.getlist()
      }, 500)
    },
    downloadError () {  //下载错误信息
      this.flag()
    },
    async getlist (obj) { //请求错误信息列表
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await this.fn({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      this.tableStatus = true
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
  },
};
</script>

<style lang='scss' scoped>
.bigBoxBox ::v-deep .v-stepper__header {
  background-color: #ffffff;
}
.load-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  height: 150px;
}

#load {
  width: 75px;
  animation: loading 3s linear infinite;
  #loading-inner {
    stroke: {
      dashoffset: 0;
      dasharray: 300;
      width: 10;
      miterlimit: 10;
      linecap: round;
    }
    animation: loading-circle 2s linear infinite;
    stroke: #0f46c1;
    fill: transparent;
  }
}

@keyframes loading {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loading-circle {
  0% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -600;
  }
}
</style>
