<template>
  <div>
    <div style="font-size:13px;color: rgba(0, 0, 0, 0.38);"
         class="pl-2">
      {{label|| $t('label_The_sorting')}}
    </div>
    <div v-if="!readonly"
         ref="inputDv"
         :style="{'background': '#F5F5F5','border-radius': '4px 4px 0 0','height':height+'px','border-bottom':!color?'1px solid rgba(0, 0, 0, 0.38)':'2px solid #0f46c1'}"
         class="d-flex justify-center align-center">
      <i class="material-icons ml-2"
         @click="remove">remove</i>
      <input type="text"
             @focus="color=true"
             @blur="color=false"
             @input='input'
             @keyup="keyup"
             :placeholder="placeholder"
             v-model="nums"
             :disabled='disabled'
             :style="{'border':'none','outline':'none','height':height+'px','width':'95%','text-align':'center'}">
      <i class="material-icons mr-2"
         @click="add">add</i>
    </div>
    <!-- 视图 -->
    <div v-else
         :style="{'height':height+'px','border-bottom':'1px solid rgba(0, 0, 0, 0.38)'}"
         class="d-flex justify-center align-center">
      <input type="text"
             readonly
             class="pl-2"
             @input='input'
             @keyup="keyup"
             :placeholder="placeholder"
             v-model="nums"
             :style="{'border':'none','outline':'none','height':height+'px',flex:1,'text-align':textAlign}">
    </div>
    <div v-if="errorMessages.length>0"
         :style="{'color': '#ff5252','font-size':'12px'}">
      {{errorMessages[0]}}
    </div>
  </div>
</template> 

<script>
/**
 * 数字输入框
 */
export default {
  components: {},
  props: {
    label: {
      type: String,
    },
    errorMessages: {
      type: Array,
      default: () => { return [] }
    },
    readonly: {  //读写状态 
      type: Boolean,
      default: false
    },
    height: {
      type: String,
      default: '44'
    },
    placeholder: {
      type: String,
      default: ''
    },
    num: {
      type: [Number, String],
      default: 1
    },
    disabled: {
      type: Boolean,
      default: false
    },
    textAlign: {
      type: String,
      default: 'start'
    }
  },
  model: {
    prop: 'num',
    event: 'changeNum'
  },
  data () {
    return {
      nums: null,
      color: false
    };
  },
  computed: {
  },
  created () { },
  watch: {
    num (val) {
      this.nums = Number(val <= 0 ? 1 : val)
    },
    errorMessages: {
      handler (val) {
        if (!this.readonly) {
          if (val.length > 0) this.$refs.inputDv.style.borderBottom = '1px solid #ff5252'
          else this.$refs.inputDv.style.borderBottom = '1px solid #0f46c1'
        }
      }, deep: true
    }
  },
  mounted () {
    this.$emit('changeNum', 1)
    if (this.errorMessages.length > 0 && !this.readonly) this.$refs.inputDv.style.borderBottom = '1px solid #ff5252'
    this.nums = Number(this.num) >= 0 ? 1 : Number(this.num)
  },
  methods: {
    input () {
      this.nums = Number(String(this.nums).replace(/[^\d]/g, ''))
      this.nums <= 0 ? this.nums = 1 : ''
      this.$emit('changeNum', Number(this.nums) <= 0 ? 1 : Number(this.nums))
    },
    remove () {
      this.$emit('changeNum', Number(this.nums) - 1 <= 0 ? 1 : Number(this.nums) - 1)
    },
    add () {
      this.$emit('changeNum', Number(this.nums) + 1 <= 0 ? 1 : Number(this.nums) + 1)
    },
    keyup () {
      // this.nums = Number(String(this.nums).replace(/[^\d]/g, ''))
      // this.$emit('changeNum', Number(this.nums))
    }
  },
};
</script>

