<template>
  <v-menu offset-y
          :disabled='disabled'
          transition="scale-transition"
          :close-on-content-click="false">
    <template v-slot:activator="{ on }">
      <span v-on="on">
        <slot name="showAction"></slot>
      </span>
    </template>
    <v-colorPicker @input='colorChange'
                   v-model="color"></v-colorPicker>
  </v-menu>
</template>

<script>
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean
    }
  },
  data () {
    return {
      color: ''
    };
  },
  model: {
    prop: 'color',
    event: 'colorChange'
  },
  created () { },
  mounted () { },
  methods: {
    colorChange () {
      this.$emit('colorChange', this.color)
    }
  },
  computed: {},
  watch: {
  },
};
</script>

