// 根据name获取字典数据  普通参照下拉 传参照名字就行
export const getDataByName = (dict_name) => {
    const params = {
        dict_name,
        '~table~': 'mrbase_sys_dict'
    }
    return new Promise((resolve, reject) => {
        http.post('/api/dictionary/list-dictionary-byname/v1', params)
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                reject(err)
            })
    })
}
