import http from '../httpConfig'
// 高级参照管理

// 高级参照列表查询
export const getList = async (params) => {
  return await http.post('/api/prompt/list-prompt/v1', params)
}
// 高级参照业务列表
export const getBusinessList = (params = {}) => {
  params['~table~'] = 'mrbase_sys_prompt'
  return new Promise((resolve, reject) => {
    http.post('/api/prompt/business/findPrompt/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}
// 高级参照详情
export const getDetail = async (params) => {
  return await http.post('/api/prompt/find-prompt/v1', params)
}
// 高级参照删除
export const getDetele = async (params) => {
  return await http.post('/api/prompt/delete-prompt/v1', params)
}
// 高级参照修改
export const getChange = async (params) => {
  return await http.post('/api/prompt/update-prompt/v1', params)
}
// 高级参照新增
export const getAdd = async (params) => {
  return await http.post('/api/prompt/add-prompt/v1', params)
}