<template>
  <!-- 高级参照 -->
  <div style="width: 100%"
       class="promptBox">
    <div v-show='!disabled'>
      <v-select v-if="!skLoading"
                :value="prompt"
                :items="prompt_arr"
                :no-data-text="noDataText"
                :item-value='value_field'
                :item-text='view_fields'
                :label="label"
                required
                :clearable='clearable'
                :multiple="multiple"
                @change='promptChange'
                :chips='chips'
                :error-messages="errMsg"
                :placeholder="placeholder || label"
                :rules="myRules"></v-select>
      <v-select :label="label"
                v-else></v-select>
    </div>
    <!-- <v-textarea
      v-show="disabled"
      :label="label"
      auto-grow
      dense
      readonly
      v-model='showName'
    ></v-textarea> -->
    <mrc_ess_input :error-messages="errMsg"
                   v-show="disabled"
                   :placeholder="placeholder || label"
                   :label="label"
                   readonly
                   v-model='showName'>
    </mrc_ess_input>
  </div>
</template> 
  
<script>  
/**
 * info 公用高级参照组件 可传单个prompt_name 或者 对象形式 多个参数
 * @params label 标签名
 * @params disabled 是否禁用
 * @params obj 请求的条件 prompt_name 等   优化 obj 可以只传一个字符串 不用写prompt_name属性了
 * @params noDataText 没有下拉显示的文字
 * @params select_arr 下拉的数据
 * @params multiple 多选
 * @params rules 为空时显示的字，默认提示为 '必填提示'
 * @params prompt_name 只有一个参照名字的参数时就可能不用传obj直接传名字
 * @params chips 选中的纸片样式
 * @params clearable 清空
 * @params errMsg 手动输入错误消息   有这个属性那 rules就必须传false
 * @params value_field 隐藏值存在的属性
 * @params view_fields 显示值存在的属性
 * @event getSelectItem 获取选中项的一列数据
 * @event  getData  select 改变的事件
 * @event getPromptData 返回下拉数组
 * 使用 <mrc_ess_mrc_ess_prompt></mrc_ess_prompt>
 */
import { getBusinessList } from '@/service/api/prompt'
export default {
  components: {},
  props: {
    value_field: {
      type: String,
      default: 'value_field'
    },
    view_fields: {
      type: String,
      default: 'view_fields'
    },
    placeholder: {
      type: String,
    },
    errMsg: {
      type: Array,
      default: () => []
    },
    clearable: {
      type: Boolean,
      default: false
    },
    chips: {
      type: Boolean,
      default: true
    },
    prompt_name: {
      type: String
    },
    label: {
      type: String
    },
    disabled: {
      type: Boolean
    },
    obj: {
      type: [Object, String]
    },
    noDataText: {
      type: String
    },
    select_arr: {
      type: Array,
      default: () => []
    },
    multiple: {
      type: Boolean
    },
    rules: {
      type: [String, Boolean]
    },
    prompt: {
      type: [Array, String, Number, null]
    },
  },
  data () {
    return {
      prompt_arr: [],
      requestData: {},
      skLoading: true,
      showName: ''
    };
  },
  model: {
    prop: 'prompt',
    event: 'getData'
  },
  created () {

  },
  async mounted () {
    const inputs = document.getElementsByClassName('v-select__slot')
    if (!this.disabled) {
      inputs.forEach(item => {
        item.style.backgroundColor = '#f2f2f2'
      })
    }
    else {
      inputs.forEach(item => {
        item.style.backgroundColor = '#ffffff'
      })
    }
    this.prompt_arr = this.select_arr
    this.skLoading = true
    if (typeof this.obj == 'string') {
      const prompt = await getBusinessList({ prompt_name: this.obj })
      if (prompt.code == 200) {
        this.prompt_arr = prompt.data
        this.$emit('getPromptData', this.prompt_arr)
      }
    } else {
      this.requestData = this.obj ? this.obj : {}
      if (Object.keys(this.requestData).length > 0) {
        const prompt = await getBusinessList({ ...this.requestData })
        if (prompt.code == 200) {
          this.prompt_arr = prompt.data
          this.$emit('getPromptData', this.prompt_arr)
        }
      }
    }

    if (this.prompt_name) {
      const prompt = await getBusinessList({ prompt_name: this.prompt_name })
      if (prompt.code == 200) {
        this.prompt_arr = prompt.data

        this.$emit('getPromptData', this.prompt_arr)
      }
    }
    this.setShowName()


    this.skLoading = false
  },
  methods: {
    promptChange (val) {
      this.setShowName()
      this.$emit('getData', val)
      const item = this.prompt_arr.filter(item => {
        // console.log(val, item.value_field);
        if (this.multiple) {
          return val.indexOf(item.value_field) > -1
        } else {
          return val == item.field
        }

      })
      if (this.multiple) {
        this.$emit('getSelectItem', item)
      } else {
        this.$emit('getSelectItem', item[0])
      }

    },
    setShowName () {
      if (Object.prototype.toString.call(this.prompt).indexOf('Array') > -1) {
        // let index = []
        let indexArr = this.prompt.reduce((totle, item) => {
          let index = this.prompt_arr.findIndex(it => {
            return it[this.value_field] == item
          })
          totle.push(index)
          return totle
        }, [])
        let html = ''
        indexArr.forEach((item, index) => {
          if (item > -1) {
            html += this.prompt_arr[item][this.view_fields] + (index == indexArr.length - 1 ? '' : '，')
          }
          this.showName = html
        })
      } else {
        const index = this.prompt_arr.findIndex(item => {
          return item[this.value_field] == this.prompt
        })

        if (index != -1) {
          this.showName = this.prompt_arr[index][this.view_fields]
        }
      }
    }
  },
  computed: {
    myRules () {
      if (this.rules == false) {
        return []
      } else {
        return [v => !!v || this.rules]
      }
    }
  },
  watch: {
    disabled (v) {
      const inputs = document.getElementsByClassName('v-select__slot')
      if (!v) {
        inputs.forEach(item => {
          item.style.backgroundColor = '#f2f2f2'
        })
      }
      else {
        inputs.forEach(item => {
          item.style.backgroundColor = '#ffffff'
        })
      }
    },
    obj: {
      async handler (val) {
        this.requestData = val
        if (typeof val == 'string') {
          const prompt = await getBusinessList({ prompt_name: val })
          if (prompt.code == 200) {
            this.prompt_arr = prompt.data
            this.$emit('getPromptData', this.prompt_arr)
          }
        } else {
          // 这里有个return 阻断执行，，报错看看这个
          for (var i in this.requestData) {
            if (i != 'prompt_name') {
              if (!this.requestData[i]) return
            }
          }
          if (!this.requestData.prompt_name) return
          const prompt = await getBusinessList({ ...this.requestData })
          if (prompt.code == 200) {
            this.prompt_arr = prompt.data
            this.skLoading = false
            this.$emit('getPromptData', this.prompt_arr)
          }
        }
        if (this.prompt) {
          this.setShowName()
        }
      },
      deep: true
    },
    select_arr: {
      handler (val) {
        this.prompt_arr = val
        this.setShowName()
      },
      deep: true
    },
    // prompt_arr: {
    //   handler(val) {
    //     console.log(val,'4asasassa')
    //   },
    //   deep:true
    // },
    prompt: {
      handler (val) {
        this.prompt_arr.length > 0 && this.setShowName()
      },
      deep: true
    }
  },
};
</script>

<style scoped>
.selectShow {
  width: 0px;
}
.inputShow {
  width: 0px;
}
.promptBox /deep/ .v-select__slot input {
  min-height: 44px;
  height: 44px;
  line-height: 44px;
  padding-left: 8px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
}
/* .promptBox /deep/ .v-select__slot label {
  font-family: Noto Sans CJK TC;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.38);
  padding-left: 8px;
} */
</style>
