//文件管理
import http from '../httpConfig'
import axios from 'axios'
const table = 'mrbase_sys_attachments'
//文件新增 上传
export const addFile = (params = {}) => {
  return new Promise((resolve, reject) => {
    http.post('/api/fileManager/add-file-record-plan/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}
//文件新增结果回调 
export const addResultFile = (params = {}) => {
  return new Promise((resolve, reject) => {
    http.post('/api/fileManager/add-file-callback/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}



