import { getBusinessList } from '@/service/api/prompt.js'
import http from '@/service/httpConfig'
import { languageList, getCurrentList } from '@/service/api/internationalization'
const actions = {
  set_token ({ commit }, value) {
    commit('set_token', value)
  },
  set_userData ({ commit }, value) {
    commit('set_userData', value)
  },
  set_currentCompany ({ commit }, value) {
    commit('set_currentCompany', value)
  },
  set_sideMenu ({ commit }, value) {
    commit('set_sideMenu', value)
  },
  // async set_topcompanylist ({ state, commit }, value) {
  //   const res = await getBusinessList({ prompt_name: 'client-levels', CLIENT_UUID: state.userData.clientUuid })
  //   if (res.code == 200) {
  //     commit('set_topcompanylist', res.data)
  //   }
  // },
  async set_topcompanylist ({ state, commit }, value) {
    const res = await http.post('/api/company/relation-company/v1',
      { '~table~': 'mrbase_sys_org_levels' })
    if (res.code == 200) {
      if (Array.isArray(res.data)) {
        commit('set_topcompanylist', res.data)
        commit('set_currentCompanyClientUuid', res.data[0].client_uuid || '')
      }
      else {
        commit('set_topcompanylist', [res.data])
        commit('set_currentCompanyClientUuid', [res.data][0].client_uuid || '')
      }
    }
  },
  // 查找该公司有几种国际化语言
  async setCurrentCompanyLanguageList ({ state, commit }, value) {
    const res = await languageList({ org_level_uuid: state.userData.org_level_uuid })
    if (res.code == 200) {
      commit('setCurrentCompanyLanguage', res.data.Data)
    } else {
      commit('setCurrentCompanyLanguage', [])
    }
  },
  // async setAllLanguageList ({ commit }) {
  //   const res = await languageList()
  //   if (res.code == 200) {
  //     commit('setAllLanguage', res.data.Data)
  //   }
  //   commit('setAllLanguage', [])
  // },
  // 查找公司的所有语言label
  async set_languageList ({ commit }, value) {
    const res = await getCurrentList({ language_code: value })
    commit('set_languageList', res.data)
  },
  resetState ({ commit }) {
    commit('resetState')
  }
}

export default actions