const getters = {
  lang_name (state) {
    const lang = state.currentCompanyLanguage.filter(item => {
      return item.value.toLowerCase() == state.lang.toLowerCase()
    })
    let text = ''
    if (lang.length > 0) {
      text = lang[0].text
    }
    return text
  },
  btnPermission: state => state.btnPermission,
  subPage_name (state) {
    const arr = state.subPage_name
    const type = Object.prototype.toString.call(arr)
    if (type.indexOf('String') > 0) {
      return arr
    }
    let newArr = []
    if (arr.length <= 0) {
      return ''
    }
    arr.forEach((item, index) => {
      newArr.push({})
      for (let i in item) {
        if (item[i] && i != 'uuid') {
          newArr[index][i] = item[i]
        }
      }
    })
    const keys = newArr.map(item => {
      return Object.keys(item)[0]
    })
    const values = newArr.map(item => {
      return Object.values(item)[0]
    })
    const currentLanguageIndex = keys.findIndex(item => item.toLowerCase() == state.lang.toLowerCase())
    return values[currentLanguageIndex]
  }
}

export default getters