<template>
  <!-- 添加汇率弹窗 -->
  <v-dialog :value="isopen"
            @click:outside='cancel'
            :width="width"
            scrollable>
    <v-card>
      <v-card-subtitle class="is_flex_between"
                       v-if="!hiddenTitle"
                       style="background: #000000;
                              color: #ffffff;
                              padding-top: 10px;
                              padding-bottom: 10px">
        <span>{{title}}</span>
        <span @click='cancel'
              class="material-icons"
              style="cursor: pointer;font-size: 20px">clear</span>
      </v-card-subtitle>
      <template v-if='noPadding'>
        <v-card-text ref="dialogContent">
          <!-- 插入内容 -->
          <slot></slot>
        </v-card-text>
      </template>
      <template v-else>
        <div ref="dialogContent">
          <slot></slot>
        </div>
      </template>
      <mrc_ess_submitBtn @cancel='cancel'
                         v-if="disabled"
                         :successText='successText'
                         style="height: auto;padding: 24px 0"
                         :cancelText='cancelText'
                         @success='success'></mrc_ess_submitBtn>
    </v-card>

  </v-dialog>
</template> 

<script>
/**
 * @params isopen 控制弹窗开关
 * @params width 弹窗宽度
 * @params title 弹窗标题 
 * @params disabled 是否隐藏按钮
 * @event cancel 取消
 * @event success 成功
 */
export default {
  components: {},
  props: {
    isopen: {
      type: Boolean,
      default: false
    },
    width: {
      type: [Number, String],
      default: 500
    },
    title: {
      type: String,
      default: '标题'
    },
    disabled: {
      type: Boolean,
      default: true
    },
    hiddenTitle: {
      type: Boolean,
      default: false
    },
    successText: {
      type: String
    },
    cancelText: {
      type: String
    },
    noPadding: {
      type: Boolean,
      default: true
    }
  },
  model: {
    prop: 'isopen',
    event: 'flag'
  },
  data () {
    return {

    };
  },
  created () { },
  mounted () {
  },
  methods: {
    success () {
      this.$refs.dialogContent.scrollTop = 0
      this.$emit('success')
    },
    cancel () {
      this.$refs.dialogContent.scrollTop = 0
      this.$emit('flag', !this.isopen)
      this.$emit('cancel', !this.isopen)
    }
  },
  computed: {},
  watch: {
  },
  beforeDestroy () {
  }
};
</script>

