<template>
  <v-switch :true-value="trueValue"
            :false-value="falseValue"
            :input-value="status"
            @change="change"
            :readonly='readonly'
            :label="showlabel"></v-switch>
</template>

<script>
/**
 * @description: 
 * @param {trueValue} true的值
 * @param {falseValue} false的值
 * @param {status} 绑定的值
 * @param {trueText} 为true显示的文本
 * @param {falseText} 为false显示的文本
 * @return: 
 * @author: lwq
 */
export default {
  components: {},
  props: {
    trueValue: {
      type: [String, Number, Boolean],
      default: true
    },
    falseValue: {
      type: [String, Number, Boolean],
      default: false
    },
    status: {
      type: [String, Number, Boolean],
      default: true
    },
    trueText: {
      type: String,
      default: '开启'
    },
    falseText: {
      type: String,
      default: '关闭'
    },
    readonly: {
      type: Boolean,
      default: false
    },
    label: {
      type: String
    }
  },
  model: {
    prop: 'status',
    event: 'change'
  },
  data () {
    return {

    };
  },
  computed: {
    showlabel () {
      if (this.label) {
        return this.label
      } else {
        return this.status == this.trueValue ? this.trueText : this.falseText
      }
    }
  },
  created () { },
  watch: {},
  mounted () {
  },

  methods: {
    change (val) {
      this.$emit('change', val)
    }
  },
};
</script>

