import http from '../httpConfig'
const table = 'mrbase_sys_flow_proxy_approval'


//人员下拉列表
export const getStaffListForFilter = (params = {}) => {
  params['~table~'] = 'mrbase_sys_staffs'
  return new Promise((resolve, reject) => {
    http.post('/api/staff-manager/find-staff_condition/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

