<template>
  <div>
    <div style="color:rgb(153, 153, 153);font-size:16px;">{{label}}</div>
    <div v-if="isShow"
         class="d-flex">
      <span class="span1 ml-1"
            style="border-top-left-radius: 6px;border-bottom-left-radius: 6px;"
            @click="change(-1)">
        -
      </span>
      <input type="text"
             :style="{width:inputWidth}"
             v-model="childrenNumber"
             @input="changeinput"
             class="span2">
      <span class="span1 mr-1"
            style="border-top-right-radius: 6px;border-bottom-right-radius: 6px;"
            @click="change(+1)">
        <v-icon class="material-icons #ffffff--text">
          add
        </v-icon>
      </span>
      <span style="height: 30px;line-height:40px;display:block"
            class="gray--text">{{endText}}</span>
    </div>
    <span v-else
          class="d-flex">
      <span :style="{width:viewWidth,borderBottom:borderBottom?'1px solid #e0e0e0':'none',lineHeight:borderBottom?'40px':'30px'}"
            style="font-size:16px;display:inline-block;text-align:center;height:30px;line-height:30px;">
        {{childrenNumber}}</span>
      <span style="height: 30px;line-height:40px;display:block"
            class="gray--text">{{endText}}</span>
    </span>
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    label: {
      type: String,
      default: ''
    },
    number: {
      type: Number,
    },
    numberText: {
      type: String,
      default: ''
    },
    endText: {
      type: String,
      default: ''
    },
    inputWidth: { //修改状态长度
      type: String,
      default: '70px'
    },
    viewWidth: { //只读状态长度
      type: String,
      default: '130px'
    },
    isShow: { //读写状态
      type: Boolean,
      default: true
    },
    borderBottom: { //是否要下划线
      type: Boolean,
      default: false
    }
  },
  model: {
    prop: 'number',
    event: 'changeNumber'
  },
  data () {
    return {
      childrenNumber: null,
    };
  },
  computed: {},
  created () { },
  watch: {
    number (val) {
      this.numberText ? this.childrenNumber = val + this.numberText : this.childrenNumber = val
    }
  },
  mounted () {
    this.numberText ? this.childrenNumber = this.number + this.numberText : this.childrenNumber = this.number
  },

  methods: {
    change (v) {
      let num = Number(String(this.childrenNumber).replace(/[^\d]/g, '')) + v
      num < 0 ? num = 0 : ''
      this.$emit('changeNumber', num)
      this.numberText ? this.childrenNumber = num + this.numberText : this.childrenNumber = num
    },
    changeinput (v) {
      const num = Number(v.target.value.replace(/[^\d]/g, ''))
      // const num = Number(v.replace(/[^\d]/g, ''))
      this.$emit('changeNumber', num)
      this.numberText ? this.childrenNumber = num + this.numberText : this.childrenNumber = num
    }
  },
};
</script>

<style scoped>
.box {
  display: flex;
}
.span1 {
  display: block;
  width: 30px;
  height: 30px;
  background-color: rgb(153, 153, 153);
  color: #ffffff;
  line-height: 30px;
  text-align: center;
  font-size: 24px;
}
.span1 .v-icon {
  color: #ffffff;
}
.span2 {
  font-size: 16px;
  outline: none;
  text-align: center;
  line-height: 30px;
  height: 30px;
  padding: 0 10px;
  max-width: 150px;
  border-top: 1px solid rgba(0, 0, 0, 0.42);
  border-bottom: 1px solid rgba(0, 0, 0, 0.42);
}
.hiddenborder {
  border-style: none;
}
