<template>
  <input type="file"
         ref="uploadInput"
         style="width: 100%;position:absolute;height:100%;top:0;left:0;opacity: 0;z-index:5"
         multiple='multiple'
         :accept="accept"
         @change="fileChange">
</template>

<script>
/**
 * accept input         接受文件类型
 * uploadFileType       验证类型
 * @changeimgSrc        文件改变事件
 */
import { get_list } from '@/service/api/func_module'
import http from '@/service/httpConfig.js'
import {
  addFile, addResultFile, deleteFile, deleteResultFile
} from '@/service/api/fileManagements.js'
export default {
  components: {},
  props: {
    accept: {
      type: String,
      default: 'image/*'
    },
    uploadFileType: {},
    // imgSrc: {},
  },
  // model: {
  //   prop: 'imgSrc',
  //   event: 'changeimgSrc'
  // },
  data () {
    return {
    };
  },
  computed: {},
  created () { },
  watch: {},
  async mounted () {
    await this.getProductUuid()
    let _this = this;
    var dropbox = this.$refs.uploadInput ? this.$refs.uploadInput : ''
    if (dropbox == '') return
    dropbox.addEventListener("drop", this.enentDrop, false)
    dropbox.addEventListener("dragleave", function (e) {
      e.stopPropagation();
      e.preventDefault();
      // _this.borderhover = false;
    })
    dropbox.addEventListener("dragenter", function (e) {
      e.stopPropagation();
      e.preventDefault();
      // _this.borderhover = true;
    })
    dropbox.addEventListener("dragover", function (e) {
      e.stopPropagation();
      e.preventDefault();
      // _this.borderhover = true
    })
  },

  methods: {
    fileChange (e) {
      if (!e.target.files.length > 0) return
      if (!this.validateUploadFileType(e.target.files)) return
      this.$emit('changeimgSrc', e.target.files)
    },
    enentDrop: function (e) {
      this.borderhover = false
      e.stopPropagation();
      e.preventDefault();  //必填字段
      // console.log(e.dataTransfer.files);
      if (!e.dataTransfer.files.length > 0) return
      if (!this.validateUploadFileType(e.dataTransfer.files)) return
      this.$emit('changeimgSrc', e.dataTransfer.files)
    },
    async uploadFiles (file) {
      if (!file.length > 0) return
      if (!this.$store.state.product_uuid) {
        await this.getProductUuid()
      }
      let res = await addFile({
        ...this.options,
        product_uuid: this.$store.state.product_uuid,
        attachment_title: file[0].name,
        attachment_size: file[0].size
      })
      if (res.code == 200) res = res.data
      else return
      let Size = 0
      const formData = new FormData()
      if (file.length > 1) { //多个文件上传 
        file.forEach(item => {
          formData.append('file', item)
          Size += item.size
        })
      }
      else {
        formData.append('file', file[0])
        Size = file[0].size
      }
      // if (res.residue_capacity < Size) return this.$Message({ title: '内存不足' })
      let url = res.attachment_domain + res.attachment_container + res.attachment_account
      console.log(url);
      this.$azureUpload({
        baseUrl: url,
        sasToken: '?sv=2019-12-12&ss=bfqt&srt=sco&sp=rwdlacupx&se=2020-09-13T14:57:09Z&st=2020-08-13T06:57:09Z&spr=https,http&sig=MmRMW06A1v52FaJEekZRRrj0dUaR5m3NZoqrLFaRulw%3D',
        file: formData,
        progress: (progress) => { },
        complete: (res) => {
          addResultFile({
            uuid: res.uuid,
            result: 1
          })
        },
        error: (error) => { },
        blockSize: file.size,
      })
    },
    async getProductUuid () { //获取ProductUuid 
      const res = await get_list()
      if (res.code == 200) {
        this.$store.commit('set_product_uuid', res.data[0].uuid)
      }
      else {
        this.$Message({
          title: '为获取到产品信息'
        })
      }
    },
    validateUploadFileType (curFileType) {  // 验证文件类型
      return this.uploadFileType.test(curFileType[0].name)
    },
  },
};
</script>

