import Vue from 'vue'
import VueI18n from 'vue-i18n'
import vuetify from './vuetify'

Vue.use(VueI18n)

let lang = navigator.language || navigator.userLanguage;//常规浏览器语言和IE浏览器
const DEFAULT_LANG = lang.toLowerCase()
const LOCALE_KEY = 'localeLanguage'


const localeslist = JSON.parse(localStorage.getItem('currentCompanyLanguage'))
let keys = {}
if (localeslist) {
  const value = localeslist.map(item => {
    return item.value.toLowerCase()
  })
  keys = {}
  value.forEach(item => {
    keys[item] = { $vuetify: vuetify[item] }
  })
} else {
  keys = {
    en: {
      $vuetify: vuetify.en
    },
    'zh-ch': {
      $vuetify: vuetify.zh
    },
    'zh-hk': {},
    de: {},
    fr: {},
    ja: {}
  }
}



const i18n = new VueI18n({
  locale: DEFAULT_LANG,
  messages: keys,
  silentTranslationWarn: true
})


export const setup_locale = lang => {
  if (!!lang) {
    lang = window.localStorage.getItem(LOCALE_KEY)
    if (keys[lang] === undefined) {
      lang = DEFAULT_LANG
    }
  }
  window.localStorage.setItem(LOCALE_KEY, lang)
  Vue.config.lang = lang
  i18n.locale = lang
}
setup_locale(window.localStorage.getItem(LOCALE_KEY) || DEFAULT_LANG)
export default i18n