<template>
  <div class="bigbox">
    <v-divider class="animated fadeIn delay-1s maxwidth" />
    <div class="sheets"
         :style="{right:left?'initial':right+'px',left:left+'px'?left:'initial'}">
      <span class="grey--text spans2"
            v-if="isShow">
        {{options.cpage==1?1:(options.cpage-1)*options.pagesize}}-
        {{this.pageStop}}{{$t('label_column')}}
        <span class="pl-1">
          (共{{options.totalCount}}{{$t('label_column')}})
        </span>
      </span>
      <v-pagination :value="options.cpage"
                    circle
                    color="grey lighten-2 black--text"
                    class="black--text"
                    prev-icon="navigate_before"
                    next-icon="navigate_next"
                    :length="options.totalpage"
                    @input='turnPage'
                    :total-visible="7" />
      <span class="grey--text spans"
            v-if="isShow">
        <!-- <span class="grey--text pr-3 spans2"> -->
        <!-- {{options.pagetext}} -->
        {{$t('label_Each_page_shows')}}
      </span>
      <v-menu offset-y
              v-if="isShow"
              small>
        <template v-slot:activator="{ on }">
          <v-btn v-on="on"
                 small
                 depressed
                 class="text-capitalize grey--text pr-0 mt-3 mb-3"
                 text
                 color="grey lighten-1"
                 outlined>
            {{options.pagesize}}
            <i class="material-icons">arrow_drop_down</i>
          </v-btn>
        </template>
        <v-list dense>
          <v-list-item v-for="(item, index) in options.selectlist"
                       :key="index"
                       @click="setPerPage(item)">
            <v-list-item-title>
              {{
             item==-1?options.pagesize:item
              }}
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </div>
  </div>
</template>
<script>
/*  @info 分页器
 *  options 结构
 *  options: {  //必填
        pagesize: 4,//条数
        cpage: this.$store.state.cpage,  //当前页数
        totalCount: null, //总条数
        totalpage: null, //总页数
        selectlist: [5, 10, 15, 20], //
        pagetext: this.$t('label_Each_page_shows') //
      }
 *  @params {right}  居右   默认居右20px
 *  @params {left }   居左  
 *  @params {isShow} 默认显示全部  false只显示分页
 *  @event change 获取 整个options 配置
 
<mrc_ess_paginationsinations v-model='options'
                         @change="turnPage"></mrc_ess_paginationsinations>
  options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
        selectlist: [5, 10, 15, 20],
        pagetext: this.$t('label_Each_page_shows')
      },
this.options.totalCount = res.totalCount
this.options.totalpage = res.totalpage
*/
export default {
  name: "mrc_ess_paginations",
  computed: {
    pageStop () {
      if (this.options.cpage == this.options.totalpage) {
        return this.options.cpage == 1 ? this.options.totalCount : (this.options.cpage - 1) * this.options.pagesize + (this.options.totalCount - (this.options.cpage - 1) * this.options.pagesize)
      }
      else {
        return this.options.cpage * this.options.pagesize
      }
    },
  },
  mounted () {
  },
  props: {
    options: {
      type: Object
    },
    right: {
      type: Number,
      default: 20
    },
    left: {
      type: Number,
      default: null
    },
    isShow: {
      type: Boolean,
      default: true
    }
  },
  model: {
    prop: 'options',
    event: 'change'
  },
  watch: {
  },
  data () {
    return {
    }
  },
  methods: {
    /**
     * @description: 改变每页条数
     * @param {type} 
     * @return: 
     * @author: lwq
     */
    setPerPage (perpage) {
      if (this.options.pagesize == perpage) return
      this.options.pagesize = perpage
      this.options.cpage = 1
      this.$emit("change", this.options);
    },
    /**
     * @description: 点击页码
     * @param {type} 
     * @return: 
     * @author: lwq
     */
    turnPage (page) {
      if (this.options.cpage == page) return
      this.options.cpage = page
      this.$emit("change", this.options);
    }
  }
};
</script>

<style scoped>
.spans {
  display: block;
  font-size: 12px;
  /* width: 150px; */
  height: 55px;
  line-height: 55px;
  white-space: normal nowrap;
  padding-left: 20px;
  padding-right: 10px;
}
.spans2 {
  display: block;
  font-size: 12px;
  /* width: 150px; */
  height: 55px;
  line-height: 55px;
  white-space: normal nowrap;
  padding-right: 20px;
}

.sheets {
  display: flex;
  position: absolute;
  /* right: 10px; */
}
.bigbox {
  height: 55px;
  position: relative;
}
.v-pagination {
  padding: 0;
  width: initial;
}
.bigbox /deep/ .v-pagination__item {
  height: 20px;
  min-width: 20px;
  font-size: 12px;
  box-shadow: initial;
}
.bigbox /deep/ .v-pagination__navigation {
  height: 30px;
  min-width: 30px;
  font-size: 12px;
  transform: scale(0.8);
  box-shadow: initial;
}
</style>
