--> 
<template>
  <div class="inputBoxss"
       style="width:100%">
    <v-text-field :filled="filled"
                  :readonly="readonly"
                  :label='label'
                  :value='value'
                  :append-icon="appendIcon"
                  :autofocus="autofocus"
                  :background-color="bgdColors"
                  :clearIcon='clearIcon'
                  :clearable='clearable'
                  :color='color'
                  :counter='counter'
                  :counterValue='counterValue'
                  :dense='dense'
                  :disabled='disabled'
                  :height='height'
                  :hint='hint'
                  :id='id'
                  :placeholder='placeholder'
                  :prefix='prefix'
                  :prepend-icon="prependIcon"
                  :reverse='reverse'
                  :rules='rules'
                  :error-messages="errorMessages"
                  :suffix='suffix'
                  :type='type'
                  :rounded='rounded'
                  :validate-on-blur="validateOnBlur"
                  @change='change'
                  @blur='blur'
                  @click='click'
                  @click:append='clickAppend'
                  @input='input'
                  @focus='focus'
                  @click:clear='clickClear'
                  @click:prepend='clickPrepred'
                  @keyup.enter='keyupEnter'
                  ref="inputs"></v-text-field>

  </div>
</template>

<script>
export default {
  components: {},
  props: {
    rounded: {
      type: Boolean,
      default: false
    },
    filled: {
      type: Boolean,
      default: false
    },
    label: {
      type: String,
      default: undefined
    },
    appendIcon: {
      type: String,
      default: undefined
    },
    autofocus: {
      type: Boolean,
      default: false
    },
    backgroundColor: {
      type: String
    },
    clearIcon: {
      type: String,
      default: '$clear'
    },
    clearable: {
      type: Boolean,
      default: false
    },
    color: {
      type: String,
      default: undefined
    },
    counter: {
      type: [String, Number, Boolean],
      default: undefined
    },
    counterValue: {
      type: Function,
      default: null
    },
    dense: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    errorMessages: {
      type: Array,
      default: () => []
    },
    height: {
      type: [String, Number],
      default: undefined
    },
    hint: {
      type: String,
      default: undefined
    },
    hideDetails: {
      type: [String, Boolean],
      default: 'auto'
    },
    id: {
      type: String,
      default: undefined
    },
    placeholder: {
      type: String,
      default: undefined
    },
    prefix: {
      type: String,
      default: undefined
    },
    prependIcon: {
      type: String,
      default: undefined
    },
    readonly: {
      type: Boolean,
      default: false
    },
    reverse: {
      type: Boolean,
      default: false
    },
    rules: {
      type: [Array],
      default: () => []
    },
    suffix: {
      type: String,
      default: undefined
    },
    type: {
      type: String,
      default: 'text'
    },
    validateOnBlur: {
      type: Boolean,
      default: false
    },
    value: {
      type: [String, Number]
    },
  },
  model: {
    prop: 'value',
    event: 'input'
  },
  data () {
    return {
      bgdColors: '#f2f2f2'
    };
  },
  created () { },
  mounted () {
    if (!this.backgroundColor) {
      if (!this.readonly) {
        this.bgdColors = '#f2f2f2'
      }
      else {
        this.bgdColors = '#ffffff'
      }
    }
    else {
      this.bgdColors = this.backgroundColor
    }
  },
  methods: {
    change (val) {
      this.$emit('change', val)
    },
    blur (val) {
      this.$emit('blur', val)
    },
    click (val) {
      this.$emit('click', val)
    },
    clickAppend (val) {
      this.$emit('click:append', val)
    },
    input (val) {
      this.$emit('input', val)
    },
    focus (val) {
      this.$emit('focus', val)
    },
    clickClear (val) {
      this.$emit('click:clear', val)
    },
    clickPrepred (val) {
      this.$emit('click:prepred', val)
    },
    'keyupEnter' (val) {
      this.$emit('keyupEnter', val)
    }
  },
  computed: {

  },
  watch: {
    readonly (v) {
      if (!this.backgroundColor) {
        if (!v) {
          this.bgdColors = '#f2f2f2'
        }
        else {
          this.bgdColors = '#ffffff'
        }
      }
    }
  },
};
</script>

<style scoped>
.inputBoxss /deep/ .v-text-field__slot input {
  min-height: 44px;
  height: 44px;
  line-height: 44px;
  padding-left: 8px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
}
