/*
* @params title 为空的时候提示
* @params arr 新增的rule判断
*/
export const text_rules = (title, arr = [], len = 50) => {
  return [
    v => !!v || title,
    ...arr,
    v => String(v).length <= len || '超过输入个数',
    // v => !/[@#$%^&()<>?/]/.test(v) || '请勿输入非法字符',
    v => !/^ +| +$/g.test(v) || '不能输入空格',
  ]
}
