export const filterRouter = (routes) => {
  // 遍历后台传来的路由字符串，转换为组件对象
  const accessedRouters = routes.filter(route => {
    if (route.component) {
      route.component = _import(route.button_component)
    }
    if (route.menusTree && route.menusTree.length) {
      route.children = filterRouter(route.menusTree)
    }
    return true
  })
  return accessedRouters
}
// 文件项目路径 
function _import (file) {
  return () => import('@/' + file)
}


