<template>
  <div>
    <div class="d-flex"
         v-if="!readonly"
         style="width:100%">
      <div style="width:178px">
        <mrc_ess_prompt :label="$t('label_The_area_code')+'*'"
                        :rules='false'
                        v-model="codes"
                        @getData='change'
                        obj="country_mobile_prefix">
        </mrc_ess_prompt>
      </div>
      <div style="flex-grow:1">
        <mrc_ess_input v-model.number="phones"
                       @input="input"
                       :error-messages="errorMessages"
                       :readonly='false'
                       :label="$t('label_The_phone')+'*'"
                       :isNational="judegNational(phone)"></mrc_ess_input>
      </div>
    </div>
    <div style="width:100%"
         v-else>
      <mrc_ess_input v-model="codePhone"
                     :readonly='true'
                     :label="$t('label_The_phone')+'*'">
      </mrc_ess_input>
    </div>
  </div>
</template>

<script>
//区号 电话组件
//  :changeCode.sync="父组件传过来的code属性值"  父组件必写
export default {
  components: {},
  props: {
    errorMessages: {
      type: Array,
      default: () => []
    },
    code: {
      type: [String, Number],
      default: ''
    },
    phone: {
      type: [Number, String],
      default: null
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  model: {
    prop: 'phone',
    event: 'changePhone'
  },
  data () {
    return {
      codes: '',
      phones: ''
    };
  },
  computed: {
    codePhone () {
      if (!this.codes && !this.phones) return null
      return '+' + this.codes + '\xa0\xa0' + String(this.phones)
    }
  },
  created () { },
  watch: {
    code (v) {
      this.codes = Number(v)
    },
    phone (v) {
      this.phones = v
    },
  },
  mounted () {
    this.codes = Number(this.code)
    this.phones = this.phone
  },

  methods: {
    change (v) {
      this.$emit('update:changeCode', v)
    },
    input (v) {
      this.$emit('changePhone', v)
    }
  },
};
</script>

