-->
<template>
  <div>
    <v-btn width="30"
           @click="!readonly&&openDialog()">
      <span style="position:absolute;top:22px;left:7px;">选人员</span>
      <v-text-field v-model="uuid"
                    style="width:60px;opacity: 0;"
                    :readonly='true'
                    :label="label"></v-text-field>
    </v-btn>
    <slot :item='viewAllList'>
      <div class="d-flex">
        <div v-for="(item,index) in viewAllList"
             :key="index">
          {{item.organization_names}}
        </div>
      </div>
    </slot>

    <mrc_ess_myDialog title='New Item'
                      @flag='flag'
                      width='680'
                      v-model="dialog"
                      :disabled='true'
                      :noPadding='false'
                      @cancel='cancel'
                      @success='success'>
      <div class="d-flex"
           style="background-color:#ffffff">
        <div style='width:336px'
             class="dv">
          <v-tabs v-model="tabs"
                  background-color="#ffffff"
                  grow
                  active-class='tabactive'
                  slider-size='50'
                  :hide-slider='true'
                  dark
                  @change='changeTabs'
                  color='primary'>
            <v-tab v-for="item in items"
                   :key="item"
                   class="text-capitalize"
                   style="color:black">
              {{ item }}
            </v-tab>
          </v-tabs>
          <v-tabs-items v-model="tabs">
            <v-tab-item>
              <mrc_ess_selectStaff :allList='treeList'
                                   :item-text='itemText'
                                   :item-key='itemKey'
                                   :staffList='staffList'
                                   :cardLoading='cardLoading'
                                   @detailStaff='detailStaff'
                                   @selectAll='selectAll'
                                   @getStaffList='getStaffList'>
              </mrc_ess_selectStaff>
            </v-tab-item>
            <v-tab-item>
              <mrc_ess_chooseTree v-model="orgTreeList"
                                  :item-text='itemText'
                                  :item-key='itemKey'
                                  ref="treeRef"
                                  maxHeight='401'
                                  @changeAllList='changeAllList'
                                  @getDetail='getDetail'></mrc_ess_chooseTree>
            </v-tab-item>
            <v-tab-item>
              <mrc_ess_selectStaff :allList='treeList'
                                   :item-text='itemText'
                                   :item-key='itemKey'
                                   :staffList='positionList'
                                   maxHeightFooter='400'
                                   :cardLoading='cardLoading'
                                   @detailStaff='detailStaff'
                                   @selectAll='selectAll'
                                   @getStaffList='getStaffList'>
                <template #default="{ item }">
                  <div class="d-flex justify-space-between align-center"
                       style="width:100%">
                    <span :style="{color:item.active?'#0F46C1': 'rgba(0, 0, 0, 0.78)','font-size':'14px'}">
                      {{INT(item.job_post_name)}}
                    </span>
                    <span v-if="item.active">
                      <v-icon class="material-icons mr-3"
                              v-if="item.active"
                              style="color:rgb(19, 166, 113);">
                        check_circle
                      </v-icon>
                    </span>
                    <span v-else></span>
                  </div>
                </template>
              </mrc_ess_selectStaff>
            </v-tab-item>
            <v-tab-item>
              <mrc_ess_sclectStaffGroup @selectAll='selectAll'
                                        ref="groupRef"
                                        @deleteGroup='deleteGroup'
                                        @findDetail='findDetail'
                                        @detailStaff='detailStaff'
                                        v-model="groupList"></mrc_ess_sclectStaffGroup>
            </v-tab-item>
          </v-tabs-items>
        </div>
        <!-- ----------- -->
        <div class="segmentation2">
        </div>
        <!-- -------------- -->
        <div style='flex:1'
             v-if="!loading">
          <div v-if="!findGroupStatus"
               class="pr-4 pt-3 pl-4">
            <div class="d-flex justify-space-between mb-3">
              <div>
                <v-icon class="material-icons"
                        :style="{color:allListLength>0?'rgb(19, 166, 113)':'','font-size':'20px'}">
                  check_circle
                </v-icon>
                <span class="ml-2"
                      :style="{'font-size':'12px','color':allListLength>0?'#0f46c1': 'rgba(0, 0, 0, 0.38)'}">Selected({{
                     allListLength}}
                  )</span>
              </div>
              <div @click="saveGroup">
                <v-icon class="material-icons"
                        :style="{color:!allListLength>0?'rgba(0, 0, 0, 0.38)':'#0f46c1','font-size':'20px'}">
                  save
                </v-icon>
                <span class="ml-2"
                      :style="{color:!allListLength>0?'rgba(0, 0, 0, 0.38)':'#0f46c1','font-size': '12px'}">Save as group</span>
              </div>
            </div>
            <div class="footer_box pt-2 mb-4 pl-3 pr-2">
              <!-- 员工-->
              <div style="height:24px;font-family: Noto Sans CJK TC;font-style: normal;font-weight: normal;font-size: 12px;line-height: 128.91%;display: flex;align-items: center;color: rgba(0, 0, 0, 0.38);flex: none;order: 4;align-self: center;font-size: 12px;"
                   v-if="checkedStaffList.length>0">Staff</div>
              <div class="d-flex mt-1 pl-2 pt-1"
                   v-for="(item,index) in checkedStaffList"
                   :key="item+index"
                   @click.stop="tabs==0?deleteItem(item,index):''"
                   style="border:1px solid #ececec;height:48px;font-size: 14px;border-radius:4px;"
                   :class="item.active?'active':''">
                <div class="ml-2">
                  <v-img style="border-radius:50%"
                         height='24'
                         width='24'
                         :src="`https://picsum.photos/500/300?image=${5 * 5 + 10}`"
                         :lazy-src="`https://picsum.photos/10/6?image=${5 * 5 + 10}`"
                         class="grey lighten-2"></v-img>
                </div>
                <div class="d-flex ml-2"
                     style="flex-direction:column">
                  <span :style="{color:item.active?'#0F46C1': 'rgba(0, 0, 0, 0.78)','font-size':'14px'}">
                    <!-- ' \xa0 ' -->
                    {{item.christian_name?item.christian_name:''}}
                    {{item.christian_name&&item.show_name?'/':''}}
                    {{item.show_name?INT(item.show_name):item.show_name}}
                  </span>
                  <span :style="{color:item.active?'#739CE8': 'rgba(0, 0, 0, 0.38)','font-size':'12px'}">
                    {{INT(item.job_post_name)}}
                  </span>
                </div>
              </div>
              <!-- 组织架构 -->
              <div style="height:24px;font-family: Noto Sans CJK TC;font-style: normal;font-weight: normal;font-size: 12px;line-height: 128.91%;display: flex;align-items: center;color: rgba(0, 0, 0, 0.38);flex: none;order: 4;align-self: center;font-size: 12px;"
                   v-if="checkedOrgList.length>0">Organization</div>
              <div class="d-flex mt-1 pl-2 pt-1"
                   v-for="(item,index) in checkedOrgList"
                   :key="item.uuid+index"
                   @click.stop="tabs==1?deleteItem(item,index):''"
                   style="border:1px solid #ececec;height:48px;font-size: 14px;border-radius:4px;"
                   :class="item.active?'active':''">
                <div class="d-flex  flex-column">
                  <span style="font-size: 14px;color: #0F46C1;"> {{item.org_level_name}}</span>
                  <span style="font-size: 10px;color: rgba(0, 0, 0, 0.38);  overflow: hidden;text-overflow: ellipsis;white-space: nowrap;"> {{item.org_level_all}}</span>
                </div>
              </div>
              <!-- 岗位列表 -->
              <div style="height:24px;font-family: Noto Sans CJK TC;font-style: normal;font-weight: normal;font-size: 12px;line-height: 128.91%;display: flex;align-items: center;color: rgba(0, 0, 0, 0.38);flex: none;order: 4;align-self: center;font-size: 12px;"
                   v-if="checkedPositionList.length>0">Position</div>
              <div class="d-flex mt-1 pl-2 pt-1 flex-column"
                   v-for="(item,index) in checkedPositionList"
                   :key="item.uuid+index"
                   @click.stop="tabs==2?deleteItem(item,index):''"
                   style="border:1px solid #ececec;height:48px;font-size: 14px;border-radius:4px;"
                   :class="item.active?'active':''">
                <span :style="{color:item.active?'#0F46C1': 'rgba(0, 0, 0, 0.78)','font-size':'14px'}">{{INT(item.job_post_name)}}</span>
                <span :style="{color:item.active?'rgba(0, 0, 0, 0.38)': 'rgba(0, 0, 0, 0.78)','font-size':'12px','overflow': 'auto'}"> {{item.org_level_all}}</span>
              </div>
              <!-- 自定义组列表 -->
              <div style="height:24px;font-family: Noto Sans CJK TC;font-style: normal;font-weight: normal;font-size: 12px;line-height: 128.91%;display: flex;align-items: center;color: rgba(0, 0, 0, 0.38);flex: none;order: 4;align-self: center;font-size: 12px;"
                   v-if="checkedGroupList.length>0">Group</div>
              <div class="d-flex mt-1 pl-2 pt-1 groupItem"
                   v-for="(item,index) in checkedGroupList"
                   :key="item.uuid+index"
                   @click.stop="tabs==3?deleteItem(item,index):''"
                   style="border:1px solid #ececec;height:48px;font-size: 14px;border-radius:4px;justify-content:space-between;align-items:center;"
                   :class="item.active?'active':''">
                <span :style="{color:item.active?'#0F46C1': 'rgba(0, 0, 0, 0.78)','font-size':'14px'}"> {{item.group_name}}</span>
                <span class="mr-2 bgd groupItemIcon"
                      @click.stop="findDetail(item)"
                      style="width:32px;height:32px;border-radius:50%;align-items: center;justify-content: center">
                  <v-icon class="material-icons"
                          :disabled='true'
                          style="font-size:18px">search</v-icon>
                </span>
              </div>
            </div>
          </div>
          <!-- --------------------------组详情=----------------------------------------- -->
          <div v-else
               class="pr-4 pt-3 pl-4">
            <div class="d-flex mb-3">
              <div @click="backList">
                <v-icon class="material-icons"
                        style="color:#0F46C1">
                  arrow_back
                </v-icon>
                <span class="ml-2"
                      style="font-size: 12px;color: #0F46C1;">
                  {{detailGroupName}}</span>
              </div>
            </div>
            <div class="footer_box pt-2 mb-4 pl-3 pr-2">
              <!-- 员工 -->
              <!-- @click.stop="tabs==0?deleteItem(item,index):''" -->
              <div class="d-flex mt-1 pl-2 pt-1"
                   v-for="(item,index) in detailGroupList"
                   :key="item+index"
                   style="border:1px solid #ececec;height:48px;font-size: 14px;border-radius:4px;"
                   :class="item.active?'active':''">
                <div class="ml-2">
                  <v-img style="border-radius:50%"
                         height='24'
                         width='24'
                         :src="`https://picsum.photos/500/300?image=${5 * 5 + 10}`"
                         :lazy-src="`https://picsum.photos/10/6?image=${5 * 5 + 10}`"
                         class="grey lighten-2"></v-img>
                </div>
                <div class="d-flex ml-2"
                     style="flex-direction:column">
                  <span :style="{color:item.active?'#0F46C1': 'rgba(0, 0, 0, 0.78)','font-size':'14px'}">
                    {{item.christian_name?item.christian_name:''}}
                    {{item.christian_name&&item.show_name?'/':''}}
                    {{item.show_name?INT(item.show_name):item.show_name}}
                  </span>
                  <span :style="{color:item.active?'#739CE8': 'rgba(0, 0, 0, 0.38)','font-size':'12px'}">
                    {{item.post_and_grade}}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div style='flex:1;min-height:500px;'
             class="d-flex align-center justify-content-center"
             v-else>
          <v-progress-circular indeterminate
                               style="margin:0 auto"
                               :size="70"
                               :width="7"
                               color="primary"></v-progress-circular>
        </div>
      </div>

      <mrc_ess_myDialog title='newItem'
                        :hiddenTitle='true'
                        width='360'
                        successText='ok'
                        :disabled='false'
                        :noPadding='false'
                        v-model="dialog2">
        <div class="groupBox">
          <div class="pl-8 pr-8 pt-8">
            <!--  -->
            <mrc_ess_input label="Group Name"
                           @input="inputGroup"
                           :error-messages="$errors(null,'addGroupName',requiredRule)"
                           v-model="addGroupName"></mrc_ess_input>
            <div v-if="showVerification"
                 style="position:relative;top:-20px;color:#ff5252;font-size:12px">组名重复,提交将覆盖以前组</div>
            <mrc_ess_submitBtn @success='successGroup'
                               successText='ok'
                               @cancel='()=>{dialog2 = false}'></mrc_ess_submitBtn>
          </div>
        </div>
      </mrc_ess_myDialog>
    </mrc_ess_myDialog>
  </div>
</template> 

<script>
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
// import { filter } from 'vuedraggable';
import {
  getChooseStaffTreeList, getStaffList, getPositionList, deleteGroup,
  addMyGroup, getDetailGroup, getSaveOrganization, addSaveOrganization, getGroupList
} from '@/service/api/chooseStaff.js'
export default {
  components: {},
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
    label: {
      type: String,
      default: 'label'
    },
    uuid: {
      type: String,
      default: ''
    },
    itemText: {
      type: String,
      default: 'org_level_name'
    },
    itemKey: {
      type: String,
      default: 'uuid'
    }
  },
  model: {
    prop: 'uuid',
    event: 'change'
  },
  data () {
    return {
      loading: false,
      tabs: 0,
      dialog: false,
      dialog2: false,
      dialog3: false,
      findGroupStatus: false,
      items: ['staff', 'Organization', 'Position', 'Saved Group'],
      selectStaffNum: 0,
      cardLoading: false,
      treeList: [],//树结构
      viewAllList: [],//页面视图展示列表
      orgTreeList: [],//组织架构树
      staffList: [],//请求的员工列表
      groupList: [], //返回的组列表
      positionList: [],//返回的岗位列表
      TsList: [],//暂存的list
      checkedStaffList: [], //用户选中的员工列表
      checkedOrgList: [], //用户选中的组织架构列表
      TsCheckedOrgList: [], //暂存组织架构列表
      checkedPositionList: [],//用户选中的岗位列表
      checkedGroupList: [], //用户选择的自定义组列表,
      detailGroupList: [],
      detailGroupName: '',
      addGroupName: '',
      showVerification: false
    };
  },
  computed: {
    groupStaffList () {
      return this.detailGroupList.filter(item => {
        return item.type == '1'
      })
    },
    groupOrgList () {
      return this.detailGroupList.filter(item => {
        return item.type == '2'
      })
    },
    groupPositionList () {
      return this.detailGroupList.filter(item => {
        return item.type == '3'
      })
    },
    allListLength () {
      return Number(this.checkedStaffList.length + this.checkedOrgList.length + this.checkedPositionList.length + this.checkedGroupList.length
      )
    },
    checkedAllList () { //用户选中的总列表
      // this.checkedOrgList = this.checkedOrgList.map(item => {
      //   item.type = '2'
      //   return item
      // })
      let arr = []
      this.checkedStaffList.map(item => {
        arr.push({
          organization_names: item.show_name,
          organization_types: '1',
          organization_uuids: item.uuid
        })
      })
      this.checkedOrgList.map(item => {
        arr.push({
          organization_names: item.org_level_name,
          organization_types: '2',
          organization_uuids: item.uuid
        })
      })
      this.checkedPositionList.map(item => {
        arr.push({
          organization_names: this.INT(item.job_post_name),
          organization_types: '3',
          organization_uuids: item.uuid
        })
      })
      this.checkedGroupList.map(item => {
        arr.push({
          organization_names: item.group_name,
          organization_types: String(item.group_type),
          organization_uuids: item.uuid
        })
      })
      return arr
      // return [...this.checkedStaffList, ...this.checkedOrgList, ...this.checkedPositionList, ...this.checkedGroupList]
    },
  },
  created () { },
  watch: {
    async uuid (v) {
      await this.getOrgList(v)
    }
  },
  async mounted () {
    await this.getOrgList(this.uuid)
  },
  methods: {
    async getOrgList (val) { //获取组织架构的详细信息
      if (val) {
        const res = await getSaveOrganization({ uuid: val })
        if (res.code == 200) {
          let arr = []
          this.checkedStaffList = []
          this.checkedOrgList = []
          this.checkedPositionList = []
          this.checkedGroupList = []
          res.data.map(item => {
            if (item.type == 1) {
              arr.push({
                organization_names: item.show_name,
                organization_types: '1',
                organization_uuids: item.uuid
              })
              this.checkedStaffList.push({ ...item, active: true })
            }
            if (item.type == 2) {
              arr.push({
                organization_names: item.org_level_name,
                organization_types: '2',
                organization_uuids: item.uuid
              })
              this.checkedOrgList.push({ ...item, active: true })
            }
            if (item.type == 3) {
              arr.push({
                organization_names: this.INT(item.job_post_name),
                organization_types: '3',
                organization_uuids: item.uuid
              })
              this.checkedPositionList.push({ ...item, active: true })

            }
            if (item.type == 4) {
              arr.push({
                organization_names: item.name,
                organization_types: item.type,
                organization_uuids: item.uuid
              })
              this.checkedGroupList.push({ ...item, active: true })
            }
            if (item.type == 5) {
              arr.push({
                organization_names: item.group_name,
                organization_types: item.type,
                organization_uuids: item.uuid
              })
              this.checkedGroupList.push({ ...item, active: true })
            }
          })
          this.viewAllList = arr
        }
      }
    },
    async changeTabs (val) { //切换选项卡  请求树or列表
      if (val == 0) { //staff
        if (!this.treeList.length > 0) {
          const res = await getChooseStaffTreeList({
            uuid: this.$store.state.currentCompany
          })
          if (res.code == 200) {
            if (!res.data) return this.treeList = []
            this.treeList = [{ ...res.data }]
          }
          else {
            this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.78)' })
          }
        }
      }
      if (val == 1) {//org
        // console.log(this.$refs.treeRef.getDetail);
        if (!this.orgTreeList.length > 0) {
          const res = await getChooseStaffTreeList({
            uuid: this.$store.state.currentCompany
          })
          if (res.code == 200) {
            if (!res.data) return this.orgTreeList = []
            this.orgTreeList = [{ ...res.data }]
            this.checkedOrgList.map(item => {
              this.$refs.treeRef.getDetail(item)
            })
          }
          else {
            this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.78)' })
          }
          this.changeItemTreeActice(this.orgTreeList)
        }
      }
      if (val == 2) {//position
        if (!this.treeList.length > 0) {
          const res = await getChooseStaffTreeList({
            uuid: this.$store.state.currentCompany
          })
          if (res.code == 200) {
            if (!res.data) return this.treeList = []
            this.treeList = [{ ...res.data }]
          }
          else {
            this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.78)' })
          }
        }
      }
      if (val == 3) { //group
        await this.getGroupList()
      }
    },
    async getGroupList () {  //获取个人组和公司组的列表
      const res = await getGroupList({
        company_uuid: this.$store.state.currentCompany
      })
      if (res.code == 200) {
        this.groupList = res.data.map(item => {
          let isNO = true
          this.checkedGroupList.map(item1 => {
            if (item1.uuid == item.uuid) {
              isNO = false
              return
            }
          })
          if (isNO) {
            return { ...item, active: false }
          }
          else {
            return { ...item, active: true }
          }
        })
      }
    },
    async getStaffList (val) {  //请求列表  
      this.cardLoading = true
      if (this.tabs == 0) { //发送请求。请求员工列表数据
        const res = await getStaffList({
          uuid: val.uuid
        })
        if (res.code == 200) {
          this.staffList = res.data.map(item => { //选中的给予状态
            item.type = '1'
            let isNo = true
            this.checkedStaffList.map(item1 => {
              if (item1.uuid == item.uuid) {
                isNo = false
                return
              }
            })
            if (isNo) {
              return { ...item, active: false }
            }
            else {
              return { ...item, active: true }
            }
          })
        }
        else {
          this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.78)' })

        }
      }
      if (this.tabs == 1) { //获取组织uuid
      }
      if (this.tabs == 2) { //发送请求。请求Positon列表数据
        const res = await getPositionList({
          uuid: val.uuid
        })
        if (res.code == 200) {
          this.positionList = res.data.map(item => {
            item.type = 3
            let isNo = true
            this.checkedPositionList.map(item1 => {
              if (item1.uuid == item.uuid) {
                isNo = false
                return
              }
            })
            if (isNo) {
              return { ...item, active: false }
            }
            else {
              return { ...item, active: true }
            }
          })
        }

      }
      if (this.tabs == 3) {
        // await this.getGroupList()
      }
      this.cardLoading = false
    },
    async detailStaff (val, index) {   //点击员工||Positon||Group的每一项
      if (this.tabs == 0) {
        if (val.active == true) {
          this.checkedStaffList.map((item, index1) => {
            if (item.uuid == val.uuid) {
              this.checkedStaffList.splice(index1, 1)
            }
          })
        }
        val.active = !val.active
        this.$set(this.staffList, index, val)
        const res = this.staffList.filter(item => {
          return item.active == true
        })
        this.checkedStaffList.push(...this.$clone(res))
        this.checkedStaffList = this.ArrayToHeavy(this.checkedStaffList)
      }
      if (this.tabs == 1) { }
      if (this.tabs == 2) {
        if (val.active == true) {
          this.checkedPositionList.map((item, index1) => {
            if (item.uuid == val.uuid) {
              this.checkedPositionList.splice(index1, 1)
            }
          })
        }
        val.active = !val.active
        this.$set(this.positionList, index, val)
        const res = this.positionList.filter(item => {
          return item.active == true
        })
        this.checkedPositionList.push(...this.$clone(res))
        this.checkedPositionList = this.ArrayToHeavy(this.checkedPositionList)
      }
      if (this.tabs == 3) { //group
        let id = null
        this.groupList.map((item, index1) => {
          if (item.uuid == val.uuid) {
            id = index1
          }
        })
        if (val.active == true) {
          this.checkedGroupList.map((item, index1) => {
            if (item.uuid == val.uuid) {
              this.checkedGroupList.splice(index1, 1)
            }
          })
        }
        val.active = !val.active
        this.$set(this.groupList, id, val)
        const res = this.groupList.filter(item => {
          return item.active == true
        })
        this.checkedGroupList.push(...this.$clone(res))
        this.checkedGroupList = this.ArrayToHeavy(this.checkedGroupList)
      }
    },
    selectAll (val, val2) { //选择列表中的全部员工  根据val2判断是全选还是全不选
      val = val.map(item => {
        item.active = !val2
        return item
      })
      if (this.tabs == 0) {
        this.staffList = val
        if (val2) {
          this.staffList.map((item, index) => {
            this.checkedStaffList.map((item1, index1) => {
              if (item.uuid == item1.uuid) {
                this.checkedStaffList.splice(index1, 1)
              }
            })
          })
        }
        else {
          this.checkedStaffList.push(...this.$clone(val))
        }
        this.checkedStaffList = this.ArrayToHeavy(this.checkedStaffList)
      }
      if (this.tabs == 1) { }
      if (this.tabs == 2) {
        this.positionList = val
        if (val2) {
          this.positionList.map((item, index) => {
            this.checkedPositionList.map((item1, index1) => {
              if (item.uuid == item1.uuid) {
                this.checkedPositionList.splice(index1, 1)
              }
            })
          })
        }
        else {
          this.checkedPositionList.push(...this.$clone(val))
        }
        this.checkedPositionList = this.ArrayToHeavy(this.checkedPositionList)
      }
      if (this.tabs == 3) {
        this.groupList = val
        if (val2) {
          this.groupList.map((item, index) => {
            this.checkedGroupList.map((item1, index1) => {
              if (item.uuid == item1.uuid) {
                this.checkedGroupList.splice(index1, 1)
              }
            })
          })
        }
        else {
          this.checkedGroupList.push(...this.$clone(val))
        }
        this.checkedGroupList = this.ArrayToHeavy(this.checkedGroupList)
      }
    },
    deleteItem (item, index) { //右边删除点击项 右边显示点击添加状态
      if (this.tabs == 0) { //staff
        this.checkedStaffList.splice(index, 1)
        this.staffList.map((item1, index1) => {
          if (item1.uuid == item.uuid) {
            this.$set(this.staffList, index1, { ...item1, 'active': false })
          }
        })
      }
      if (this.tabs == 1) {//org
        this.checkedOrgList.splice(index, 1)
        this.recursionChangeActive(this.orgTreeList, item.uuid)
      }
      if (this.tabs == 2) { //岗位
        this.checkedPositionList.splice(index, 1)
        this.positionList.map((item1, index1) => {
          if (item1.uuid == item.uuid) {
            this.$set(this.positionList, index1, { ...item1, 'active': false })
          }
        })
      }
      if (this.tabs == 3) {//group
        this.checkedGroupList.splice(index, 1)
        this.groupList.map((item1, index1) => {
          if (item1.uuid == item.uuid) {
            this.$set(this.groupList, index1, { ...item1, 'active': false })
          }
        })
      }
    },
    // -------------------------org的方法----------------------------
    recursionChangeActive (arr, uuid) { //递归调用改变点击项的状态
      arr.map((item, index) => {
        if (item.uuid == uuid) {
          this.$set(arr, index, { ...item, active: false })
          if (item.children && item.children.length > 0) {
            this.recursionActive(item.children, false)
          }
        }
        if (item.children && item.children.length > 0) {
          this.recursionChangeActive(item.children, uuid)
        }
      })
    },
    recursionActive (arr, val) { //递归将子节点变为 父节点的状态
      arr.map((item, index) => {
        this.$set(arr, index, { ...item, active: val })
        if (item.children && item.children.length > 0) {
          this.recursionActive(item.children, val)
        }
      })
    },
    changeItemTreeActice (val) {  //改变树状态
      val.map(item => {
        item.type = '2'
        item.active = false
        if (item.children && item.children.length > 0) {
          this.changeItemTreeActice(item.children)
        }
      })
    },
    filterTree (val) { //递归选择符合条件的树
      val.map(item => {
        if (item.active) {
          this.TsCheckedOrgList.push(item)
        }
        else {
          if (item.children && item.children.length > 0) {
            this.filterTree(item.children)
          }
        }
      })
    },
    changeAllList (val) {//org   
      this.TsCheckedOrgList = []
      this.filterTree(val)
      this.checkedOrgList = this.TsCheckedOrgList
    },
    getDetail (val) {  //获取当前点击树的一项  orgs
      // console.log(this.treeList);
    },
    // -------------组------------------
    async findDetail (val) { //查看组的详情 发送请求
      this.findGroupStatus = true
      this.detailGroupName = val.group_name
      const res = await getDetailGroup({
        uuid: val.uuid
      })
      if (res.code == 200) {
        this.detailGroupList = res.data
      }
    },
    backList () {
      this.findGroupStatus = false
    },
    // --------------------其他方法--------------------------
    ArrayToHeavy (arr) { //数组对象去重
      let object = {};
      let newArr = []
      newArr = arr.reduce((item, next) => {
        object[next.uuid] ? "" : object[next.uuid] = true && item.push(next);
        return item
      }, [])
      return newArr
    },
    flag () {
      this.tabs = 0
      this.orgTreeList = []
      this.staffList = []
      this.groupList = []
      this.positionList = []
    },
    cancel () {
      this.tabs = 0
      this.$emit('cancel')
      this.orgTreeList = []
      this.staffList = []
      this.groupList = []
      this.positionList = []
      this.dialog = false
    },
    async success () { //总提交
      if (this.uuid) { //修改
        const res = await addSaveOrganization({
          uuid: this.uuid,
          organizational_sub: this.checkedAllList
        })
        if (res.code == 200) {
          this.getOrgList(this.uuid)
          this.dialog = false
        }
        else {
          this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.78)' })
        }
      }
      else {  //新增
        this.$emit('success', this.checkedAllList)
        this.dialog = false
        // const res = await addSaveOrganization({
        //   organizational_sub: this.checkedAllList
        // })
        // if (res.code == 200) {
        //   this.$emit('change', res.data.uuid)
        //   this.dialog = false
        // }
        // else {
        //   this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.78)' })
        // }
      }
    },
    async openDialog () {
      this.loading = true
      this.dialog = true
      this.changeTabs(0)
      await this.getOrgList(this.uuid)
      this.loading = false
    },
    saveGroup () {
      if (this.allListLength > 0) {
        this.dialog2 = true
      }
    },
    async successGroup () { //保存自己建的组   需替换请求
      this.$v.addGroupName.$touch()
      if (this.$v.addGroupName.$error) return
      this.$emit('successGroup', this.checkedAllList)
      const res = await addMyGroup({
        group_type: '4',
        mrbase_sys_group_sub: this.checkedAllList,
        group_name: this.addGroupName
      })
      if (res.code == 200) {
        this.$Message({ text: res.msg })
        await this.getGroupList()
      }
      else {
        this.$Message({ text: res.msg, color: 'rgb(0,0,0,0.78)' })
      }
      this.dialog2 = false
    },
    deleteGroup (val) { //删除个人组
      this.$myConfirm({
        content: 'Delete Saved Group'
      })
        .then(async resss => {
          let arr = []
          val.map(item => {
            arr.push(item.uuid)
          })
          const res = await deleteGroup({
            uuids: arr.join()
          })
          if (res.code == 200) {
            await this.getGroupList()
            this.$refs.groupRef.deleteStatus = false
          }
          else {
            this.$Message({ text: err.msg, color: 'rgb(0,0,0,0.78)' })
          }
        })
        .catch(err => {
        })
    },
    async inputGroup (v) { //验证组名是否重复
      if (!v) return this.showVerification = false
      if (!this.groupList.length > 0) {
        await this.getGroupList()
      }
      if (this.groupList.some(item => {
        return item.group_name == v && item.group_type == '4'
      })) {
        this.showVerification = true
      }
      else {
        this.showVerification = false
      }
    }
  },
  validations: {
    addGroupName: {
      required
    }
  }
};
</script>

