<template>
  <div style="width:100%">
    <span class="ml-2">{{label ? label : $t('label_Transfer_personnel')+'*'}}</span>
    <v-menu offset-y
            :disabled='disabled'
            :close-on-content-click='false'>
      <template v-slot:activator="{ on, attrs }">
        <span>
          <span class="d-flex justify-space-between"
                v-if="list.uuid"
                v-bind="attrs"
                v-on="on">
            <span class="d-flex ml-2">
              <v-img src="https://picsum.photos/510/300?random"
                     width="50"
                     style="border-radius: 50%"
                     height="50"></v-img>
              <span class="d-flex flex-column ml-2">
                <span style="color:#1f1f1f"
                      class="black--text">{{list.christian_name?list.christian_name+' \xa0/\xa0 ':''}}{{list.show_name}}
                </span>
                <span style="color:#969696;width:350px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;">
                  {{list.org_level_uuid}}> {{list.dept_uuid}}> {{list.role_uuids}}
                </span>
              </span>
            </span>
            <span style="display:block;line-height: 50px;">
              <v-icon class="material-icons"
                      v-if="iconShow">expand_more</v-icon>
            </span>
          </span>
          <!-- ----------------- -->
          <span v-else
                v-bind="attrs"
                v-on="on"
                class="d-flex justify-space-between"
                style="background-color:#eeeef0;border-bottom:1px solid black">
            <span class="pl-2"
                  style="height:50px;line-height:50px;color:#969696">
              {{placeholder ?placeholder : $t('label_Employee_name_number')}}
            </span>
            <span style="display:block;line-height: 50px;">
              <v-icon v-if="iconShow"
                      class="material-icons">expand_more</v-icon>
            </span>
          </span>
          <span class="red--text"
                style="font-size:14px;display: flex; height: 21px;"
                transition="slide-x-transition">{{ErrMsg[0]}}</span>
        </span>
      </template>
      <v-list style="height:300px">
        <v-list-item v-for="(item, index) in selectlist"
                     :key="index"
                     @click="selectStaff(item)"
                     class="pl-0 py-1"
                     style="background-color:#ffffff">
          <v-list-item-title>
            <span class="d-flex justify-space-between">
              <span class="d-flex ml-2">
                <v-img src="https://picsum.photos/510/300?random"
                       width="50"
                       height="50"
                       style="border-radius: 50%"></v-img>
                <span class="d-flex flex-column ml-2">
                  <span class="black--text"
                        style="color:#1f1f1f">{{item.christian_name?item.christian_name+' \xa0/\xa0 ':''}}{{item.show_name}}
                  </span>
                  <span style="color:#969696;width:350px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;">
                    {{item.org_level_uuid}}> {{item.dept_uuid}}> {{item.role_uuids}}
                  </span>
                </span>
              </span>
              <span style="display:block;line-height: 50px;">
                {{$t('label_numberJobs')}}
              </span>
            </span>
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template> 
 
<script>
import { getBusinessList } from '@/service/api/prompt.js'
export default {
  components: {},
  props: {
    staffuuid: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    label: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    iconShow: {
      type: Boolean,
      default: false
    },
    ErrMsg: {
      type: Array,
      default: () => ['']
    },
    listSelect: { //传入列表
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      selectlist: []
    };
  },
  model: {
    prop: 'staffuuid',
    event: 'change'
  },
  computed: {
    list () {
      // console.log(this.staffuuid, this.selectlist);
      const items = this.selectlist.filter(item => item.uuid == this.staffuuid)
      this.$emit('changeList', items.length > 0 ? items[0] : {})
      // console.log(items);
      return items.length > 0 ? items[0] : {}
    }
  },
  created () { },
  watch: {
    listSelect: {
      handler (val) {
        this.selectlist = this.listSelect
      },
      deep: true
    },
  },
  async mounted () {
    if (this.listSelect.length > 0) {
      this.selectlist = this.listSelect
    }
    else {
      await this.getselectlist()
    }
  },

  methods: {
    selectStaff (item) {
      // console.log(item);
      this.$emit('change', item.uuid)
      this.$emit('changeList', item)
    },
    async getselectlist () {
      const currentCompany = this.$store.state.currentCompany
      const res = await getBusinessList({
        prompt_name: 'recipients_staff', uuid: currentCompany
      })
      if (res.code == 200) {
        this.selectlist = res.data
      }
    },
  },
};
</script>

<style scoped>
.span3 {
  width: 350px;
  overflow: hidden; /*超出部分隐藏*/
  white-space: nowrap; /*不换行*/
  text-overflow: ellipsis; /*超出部分省略号显示*/
}
</style>
