<!--
 * @Author: lwq
 * @Date: 2020-07-01 16:48:43
 * @LastEditTime: 2020-09-08 18:03:18
 * @LastEditors: Please set LastEditors
 * @Description: 日期选择器
 * @FilePath: \mrc-base-server-view\src\components\mrc_ess_datePicker.vue
--> 
<template>
  <v-text-field v-if="disabled"
                v-model="computedDateFormatted"
                :label="label"
                :placeholder="placeholder"
                :error-messages="errMsg"
                color="primary"
                append-icon="mdi-table-large"
                :disabled='unallowable'
                readonly></v-text-field>
  <v-menu v-else
          v-model="menu2"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px">
    <template v-slot:activator="{ on, attrs }">
      <mrc_ess_input v-model="computedDateFormatted"
                     :label="label"
                     color="primary"
                     append-icon="mdi-table-large"
                     readonly
                     :disabled='unallowable'
                     :placeholder="placeholder"
                     :error-messages="errMsg"
                     v-bind="attrs"
                     v-on="on"></mrc_ess_input>
    </template>
    <!-- locale -->
    <v-datePicker color="primary"
                  v-model="computedDateFormatted"
                  no-title
                  :locale='lang'
                  @input="menu2 = false"></v-datePicker>
  </v-menu>
</template>

<script>
/**
 * @params label 标题
 * @params dateParse 传入的时间 时间戳格式
 * @params format 时间格式
 * @params disabled 是否禁用 只读状态
 * @params errMsg 手动错误提示
 * @params unallowable 禁用状态 比只读高一级
 * @event getTime 时间改变获取时间
 */
import moment from 'moment'
export default {
  props: {
    label: {
      type: String,
      default: '日期'
    },
    dateParse: {
      // type: Number,
      // default: new Date().valueOf()
    },
    placeholder: {
      type: String
    },
    format: {
      type: String,
      default: 'YYYY-MM-DD'
    },
    disabled: {
      type: Boolean,
      default: true
    },
    errMsg: {
      type: Array,
      default: () => []
    },
    unallowable: {
      type: Boolean,
      default: false
    }
  },
  data: vm => ({
    menu2: false,
  }),
  mounted () {

  },
  computed: {
    lang () {
      return this.$store.state.lang
    },
    computedDateFormatted: {
      get () {
        return this.formatDate(this.dateParse)
      },
      set (val) {
        this.$emit('getTime', val)
      }
    },
  },
  model: {
    prop: 'dateParse',
    event: 'getTime'
  },
  watch: {
    menu2 (val) {
      if (!val) {
        this.$emit('blur')
      }

    }
  },

  methods: {
    formatDate (date) {
      if (!date) return null

      return moment(date).format(this.format)
    }
  },
}
</script>

<style scoped>
</style>

