/**
 * 1、addBtn 添加按钮
2、c_s_btns 取消 提交 按钮
3、confirm 二次确认弹窗
4、iconGroup 返回 修改 删除 图标
5、prompt 高级参照下拉框
6、skLoaders 骨架屏
7、menu-list v-menu list
8、mrc_ess_colorPicker 颜色选择器
9、mrc_ess_dictName 普通参照
10、 mrc_ess_mytree 权限列表 员工列表
11、mrc_ess_myDialog 弹窗模板
12、my-pagination 分页器
13、mrc_ess_datePicker 日期选择器
14、mrc_ess_selectStaff 选人
15、mrc_ess_chooseStaff 选人员input+弹出框
 */
/** 
 * $http axios请求
 * $text_rules 公用校验rules
 * $clone 深拷贝
 * $setup_locale 设置语言
 * $confirm 弹窗提示
 * $myVerify 校验规则
 * 
 */

export default {
  install (Vue) {
    Vue.component('mrc_ess_addNumInput', mrc_ess_addNumInput)
    Vue.component('menu-list', menuList)
    Vue.component('my-pagination', pagination)
    Vue.component('mrc_ess_multiText', mrc_ess_multiText)
    Vue.component('mrc_ess_addBtn', mrc_ess_addBtn)
    Vue.component('mrc_ess_iconGroup', mrc_ess_iconGroup)
    Vue.component('mrc_ess_submitBtn', mrc_ess_submitBtn)
    Vue.component('mrc_ess_prompt', mrc_ess_prompt)
    Vue.component('mrc_ess_skLoaders', mrc_ess_skLoaders)
    Vue.component('mrc_ess_colorPicker', mrc_ess_colorPicker)
    Vue.component('mrc_ess_dictName', mrc_ess_dictName)
    Vue.component('mrc_ess_inputPopUpBox', mrc_ess_inputPopUpBox)
    Vue.component('mrc_ess_mytree', myTree)
    Vue.component('mrc_ess_myDialog', mrc_ess_myDialog)
    Vue.component('mrc_ess_datePicker', mrc_ess_datePicker)
    Vue.component('mrc_ess_myStep', mrc_ess_myStep)
    Vue.component('mrc_ess_input', mrc_ess_input)
    Vue.component('mrc_ess_noContent', mrc_ess_noContent)
    Vue.component('mrc_ess_table', mrc_ess_table)
    Vue.component('mrc_ess_paginations', mrc_ess_paginations)
    Vue.component('mrc_ess_searchInput', mrc_ess_searchInput)
    Vue.component('mrc_ess_brandupload', mrc_ess_brandupload)
    Vue.component('mrc_ess_tabs', mrc_ess_tabs)
    Vue.component('mrc_ess_switch', mrc_ess_switch)
    Vue.component('mrc_ess_staffMsg', mrc_ess_staffMsg)
    Vue.component('mrc_ess_staffSpan', mrc_ess_staffSpan)
    Vue.component('mrc_ess_selectList', mrc_ess_selectList)
    Vue.component('mrc_ess_selectStaff', mrc_ess_selectStaff)
    Vue.component('mrc_ess_chooseStaff', mrc_ess_chooseStaff)
    Vue.component('mrc_ess_fileInput', mrc_ess_fileInput)
    Vue.component('mrc_ess_NumInput', mrc_ess_NumInput)
    Vue.component('mrc_ess_showClientType', mrc_ess_showClientType)
    Vue.component('mrc_ess_phoneInput', mrc_ess_phoneInput)
    Vue.component('mrc_ess_importFile', mrc_ess_importFile)
    Vue.component('mrc_ess_uploadBtn', mrc_ess_uploadBtn)
    Vue.component('mrc_ess_autoCompleteOrg', mrc_ess_autoCompleteOrg)
    Vue.component('mrc_ess_autoCompleteStaff', mrc_ess_autoCompleteStaff)
    Vue.component('mrc_ess_chooseTree', mrc_ess_chooseTree)
    Vue.component('mrc_ess_sclectStaffGroup', mrc_ess_sclectStaffGroup)
    Vue.component('mrc_ess_autoComplete', mrc_ess_autoComplete)
    Vue.component('mrc_ess_autoCompletePosition', mrc_ess_autoCompletePosition)
    Vue.component('mrc_ess_autoCompleteJobLevel', mrc_ess_autoCompleteJobLevel)
    Vue.component('mrc_ess_autoCompletePositionAndLevel', mrc_ess_autoCompletePositionAndLevel)
    Vue.component('mrc_ess_autoCompleteProduct', mrc_ess_autoCompleteProduct)
    Vue.component('mrc_ess_autoCompleteModule', mrc_ess_autoCompleteModule)
    Vue.component('mrc_ess_advanceFilterAndSearchInput', mrc_ess_advanceFilterAndSearchInput)
    Vue.component('mrc_ess_addNum', mrc_ess_addNum)
    Vue.component('mrc_ess_timePicker', mrc_ess_timePicker)
    Vue.component('mrc_ess_dateTimePicker', mrc_ess_dateTimePicker)
    Vue.component('mrc_ess_taber', mrc_ess_taber)
    Vue.component('mrc_ess_yearPicker', mrc_ess_yearPicker)
    Vue.component('mrc_ess_autoCompleteDepartment', mrc_ess_autoCompleteDepartment)

    Vue.prototype.$http = http
    Vue.prototype.$text_rules = text_rules
    Vue.prototype.$clone = clone
    Vue.prototype.$setup_locale = setup_locale
    Vue.prototype.$myConfirm = myConfirm.install
    Vue.prototype.$Message = Message.install
    Vue.prototype.$myVerify = myVerify
    Object.keys(filters).forEach(key => {
      Vue.prototype[key] = filters[key]//插入方法名和对应方法
    })
  }
} 
