import http from '../httpConfig'

const table = 'mrbase_sys_function_buttons'

// 列表
export const btnPermissionList = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/functions/onload-button/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}
