<template>
  <div>
    <div class="bigbox"
         v-if="fileInfo.path">
      <mrc_ess_fileInput :accept='accept'
                         :uploadFileType='uploadFileType'
                         @changeimgSrc='changeimgSrc'></mrc_ess_fileInput>
      <div class="pr-7">
        <v-img :src="fileInfo.path"
               width="120"
               height="120"
               class="grey darken-4"></v-img>
      </div>
      <div class="d-flex justify-space-between"
           style="width:100%">
        <span>
          <span>{{fileInfo.name}}</span><br>
          <span>{{fileInfo.size|unitConversion}}</span>
        </span>
        <span @click="deleteicon"
              style="position:relative;z-index:10;"
              class="pr-3">
          <v-icon>
            mdi-delete</v-icon>
        </span>
      </div>
    </div>

    <div class="box"
         v-else>
      <mrc_ess_fileInput :accept='accept'
                         :uploadFileType='uploadFileType'
                         @changeimgSrc='changeimgSrc'></mrc_ess_fileInput>
      <v-icon class="material-icons"
              style=" color: #e0e0e0; font-size: 55px;">
        cloud_upload
      </v-icon>
      <span class="mb-2 d-flex">
        <span>{{$t('label_dropFilesToUpload')}},{{$t('label_or')}}</span>
        <span class="span2 d-block"
              style="position:relative;top:-10px">
          <v-btn rounded
                 color="primary"
                 class="ml-3 text-capitalize"
                 dark>{{$t('label_browse')}}</v-btn>
        </span>
      </span>
      <span style="color: #a4a4a4;">
        {{UploadFormatText ? UploadFormatText: '*'+$t('label_bestResolution')+'96*96 px,PNG'}}
      </span>
    </div>
  </div>
</template>

<script>
/* 
* @params fileInfo  接受文件信息
* @params uploadFileType 上传文件类型验证
* @event deleteicon 删除商家图标事件
* @event changeisShow 隐藏
* @changeimgSrc  返回file对象
*/
export default {
  components: {},
  props: {
    fileInfo: { //file文件信息
      type: Object,
      default: () => { return {} }
    },
    uploadFileType: {  //上传文件规则
      default: () => { return /\.(png|jpg|gif|jpeg|webp)$/ }
    },
    UploadFormatText: {
      type: String
    },
    accept: {
      type: String,
      default: 'image/*'
    },
  },
  model: {
    prop: 'fileInfo',
    event: 'changePath'
  },
  filters: {
    unitConversion (v) {
      return (Number(v) / 1024 / 1024).toFixed(2) + 'M'
    }
  },
  data () {
    return {
      // srcimg: '',
    };
  },
  computed: {},
  created () { },
  watch: {

  },
  mounted () {
    // console.log(this.fileInfo);
  },

  methods: {
    deleteicon (v) {
      this.$emit('changePath', {
        path: '',
        name: '',
        size: ''
      })
      this.$emit('deleteicon', v)
    },
    changeimgSrc (v) {
      this.$emit('changePath', {
        path: window.URL.createObjectURL(v[0]),
        name: v[0].name,
        size: v[0].size
      })
      this.$emit('changeimgSrc', v)
    },
    fileChange (v) {
    }
  },
};
</script>

<style scoped>
.bigbox {
  position: relative;
  background-color: #ffffff;
  width: 100%;
  min-height: 150px;
  border: 4px solid #e0e0e0;
  padding-left: 8px;
  display: flex;
  justify-items: center;
  align-items: center;
}
.box {
  position: relative;
  background-color: #ffffff;
  border: 4px dashed #e0e0e0;
  width: 100%;
  min-height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
