<template>
  <div>
    <v-menu offset-y
            v-model="showMenu"
            z-index='300'
            :disabled='disabled'
            :close-on-content-click='!multiple'>
      <template v-slot:activator="{ on, attrs }">
        <div>
          <!-- 单选 -->
          <div v-if="!multiple"
               style="width:100%">
            <div style="font-size:13px;"
                 class="pl-2">
              {{label}}
              <slot name="label"></slot>
            </div>
            <div class="d-flex justify-space-between align-center pl-2"
                 v-bind="attrs"
                 ref="inputDv"
                 v-on="on"
                 :style="{width:'100%','background-color':disabled?'':'#F5F5F5','border-bottom':!color?'1px solid rgba(0, 0, 0, 0.38)':'2px solid #0f46c1','border-radius':' 4px 4px 0px 0px'}">
              <div class="d-flex align-center">
                <div v-if="viewList.src"
                     class="mr-2">
                  <v-img :src="viewList.src"
                         width="32"
                         style="border-radius: 50%"
                         height="32"></v-img>
                </div>
                <div style="font-size: 16px;color: rgba(0, 0, 0, 0.87)"
                     class="d-flex">
                  <span style="font-size: 16px;color: rgba(0, 0, 0, 0.87);">{{viewList.show_name}}</span>
                  <span class="mr-2 ml-2"
                        v-if="viewList.show_name&&viewList.christian_name">/</span>
                  <span>{{viewList.christian_name}}</span>
                </div>
              </div>
              <input type="text"
                     @focus="color=true"
                     @blur="color=false"
                     @input='input'
                     :placeholder="viewList.show_name||viewList.christian_name?'':(placeholder?placeholder:$t('label_Employee_name'))"
                     v-model="search"
                     :disabled='disabled'
                     style="border:none;outline:none;height:44px;flex:1;">
              <div style="line-height:44px"
                   v-if="!disabled">
                <v-icon class="material-icons"
                        style="font-size:28px;">{{!showMenu?'arrow_drop_down':'arrow_drop_up'}} </v-icon>
              </div>
            </div>
          </div>
          <!-- 多选 -->
          <div v-else
               style="width:100%">
            <div style="font-size:13px;"
                 class="pl-2">
              {{label}}
              <slot name="label"></slot>
            </div>
            <div class="d-flex pl-2"
                 v-bind="attrs"
                 ref="inputDv"
                 v-on="on"
                 :style="{width:'100%','background-color':disabled?'':'#F5F5F5','border-bottom':!color?'1px solid rgba(0, 0, 0, 0.38)':'2px solid #0f46c1','border-radius':' 4px 4px 0px 0px'}">
              <div class="d-flex align-center">
                <v-chip class="mr-3"
                        @click:close='closeChip(item)'
                        v-for="item in viewList"
                        :key="item.uuid"
                        :close='!disabled'>
                  <v-img src=""
                         width="24"
                         style="border-radius: 50%"
                         height="24"></v-img>
                  <span class="ml-2 "
                        style="font-size: 16px;color: rgba(0, 0, 0, 0.87)">
                    <span>{{item.show_name}}</span>
                    <span class="mr-1 ml-1">/</span>
                    <span>{{item.christian_name}}</span>
                  </span>
                </v-chip>
              </div>
              <div class="d-flex justify-space-between align-center"
                   style="width:100%">
                <input type="text"
                       @focus="color=true"
                       @blur="color=false"
                       :placeholder="viewList.length>0?'':(placeholder?placeholder:$t('label_Employee_name'))"
                       :disabled='disabled'
                       @input='input'
                       v-model="search"
                       style="border:none;outline:none;height:44px;width:100%;">
                <div style="line-height:44px"
                     v-if="!disabled">
                  <v-icon class="material-icons"
                          style="font-size:28px;">{{!showMenu?'arrow_drop_down':'arrow_drop_up'}} </v-icon>
                </div>
              </div>
            </div>
          </div>
          <div v-if="errMsg&&errMsg.length>0"
               style="color: #ff5252;font-size:12px;">
            {{errMsg[0]}}
          </div>
        </div>
      </template>
      <!-- ----------------------------下拉列表 -------------------------------------->
      <v-list style="max-height:361px">
        <v-list-item v-for="(item, index) in selectList"
                     :style="{background:item.checked&&multiple? '#E5EEFF':'#ffffff'}"
                     :key="index"
                     @click="selectItem(item)"
                     class="pl-0">
          <v-list-item-title>
            <!-- 单选 -->
            <div v-if="!multiple"
                 style="height:64px;width:100%"
                 class="d-flex align-center">
              <div class="mr-4 ml-4">
                <v-img src=""
                       width="48"
                       style="border-radius: 50%"
                       height="48"></v-img>
              </div>
              <div style="width:100%">
                <div class="d-flex justify-space-between align-center mb-1">
                  <span style="font-size: 16px;color: rgba(0, 0, 0, 0.87)">
                    {{item.show_name}}
                    <span class="mr-2 ml-2"
                          v-if="item.show_name&&item.christian_name">/</span>
                    {{item.christian_name}}
                  </span>
                  <span style="font-size: 14px;color: rgba(0, 0, 0, 0.38);position: relative;top: 9px;">{{item.user_id}}</span>
                </div>
                <div>
                  <span style="font-size: 12px;color: rgba(0, 0, 0, 0.38)">{{item.post_and_grade}}</span>
                  <span class="mr-2 ml-2">-</span>
                  <span style="font-size: 12px;color: rgba(0, 0, 0, 0.38)">{{item.client_and_org}}</span>
                </div>
              </div>
            </div>
            <!-- 多选 -->
            <div v-if="multiple"
                 :style="{height:'72px',width:'100%'}"
                 class="d-flex align-center">
              <div>
                <v-checkbox class="mx-2"
                            v-model="item.checked"
                            @change="changeChecked(item)"></v-checkbox>
              </div>
              <div class="mr-4">
                <v-img src=""
                       width="48"
                       style="border-radius: 50%"
                       height="48"></v-img>
              </div>
              <div style="width:100%">
                <div class="d-flex justify-space-between align-center mb-1">
                  <span style="font-size: 16px;color: rgba(0, 0, 0, 0.87)">
                    {{item.show_name}}
                    <span class="mr-2 ml-2"
                          v-if="item.show_name&&item.christian_name">/</span>
                    {{item.christian_name}}
                  </span>
                  <span style="font-size: 14px;color: rgba(0, 0, 0, 0.6)">{{item.user_id}}</span>
                </div>
                <div>
                  <span style="font-size: 14px;color: rgba(0, 0, 0, 0.6)">{{item.post_and_grade}}</span>
                  <span class="mr-2 ml-2">-</span>
                  <span style="font-size: 14px;color: rgba(0, 0, 0, 0.6)">{{item.client_and_org}}</span>
                </div>
              </div>
            </div>

          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>

<script>
/**
 * 单选多选人员组件 AutoComplete
 * 
 */
import { getStaffListForFilter } from '@/service/api/autoComplete.js'
export default {
  components: {},
  props: {
    label: {
      type: String,
      default: 'Staff'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: { //多选or单选 默认单选
      type: Boolean,
      default: false
    },
    staffUuid: { //员工uuid
      type: String
    },
    filter: { //过滤条件 默认为空
      type: String,
    },
    dropDownList: { //请求函数
      type: Array,
      default: () => []
    },
    placeholder: {
      type: String
    },
    errMsg: { //验证
    }
  },
  model: {
    prop: 'staffUuid',
    event: 'changeStaff'
  },
  data () {
    return {
      color: false,
      showMenu: false,
      allList: [], //所有员工列表
      selectList: [], //过滤后的员工列表
      viewList: {}, //用户选中的员工  数组or对象
      search: '', //搜索字段
    };
  },
  computed: {},
  async created () {
  },
  watch: {
    staffUuid (v) {
      this.getStaffInfo()
    },
    errMsg: {
      handler (val) {
        if (val.length > 0) this.$refs.inputDv.style.borderBottom = '1px solid #ff5252'
        else this.$refs.inputDv.style.borderBottom = '2px solid #0f46c1'
      }, deep: true
    }
  },
  async mounted () {
    await this.getSelectList()
    this.selectList = this.allList
    this.getStaffInfo()
  },
  methods: {
    selectItem (val) {
      if (!this.multiple) {
        this.$emit('changeStaff', val.uuid)
        this.$emit('changeStaffList', val)
        this.search = ''
      }
    },
    closeChip (val) {
      this.changeViewList(val)
    },
    changeViewList (val) {
      this.viewList = this.viewList.filter(item => {
        return item.uuid != val.uuid
      })
      this.$emit('changeStaffList', this.viewList)
      let res = this.viewList.map(item => {
        return item.uuid
      })
      this.$emit('changeStaff', res.join())
    },
    changeChecked (val) {
      if (val.checked) {
        this.viewList.push(val)
        this.$emit('changeStaffList', this.viewList)
        let res = this.viewList
        res = res.map(item => {
          return item.uuid
        })
        this.$emit('changeStaff', res.join())
      }
      else {
        if (this.viewList.length > 0) {
          this.changeViewList(val)
        }
        else {
          this.viewList = []
          this.$emit('changeStaffList', this.viewList)
          this.$emit('changeStaff', '')
        }
      }
    },
    input () { //搜索
      if (this.search != '') {
        let arr = []
        this.allList.map(item => {
          let isNo = false
          if (item.show_name.includes(this.search)) {
            let isNo = true
            arr.push(item)
          }
          if (item.christian_name.includes(this.search)) {
            if (!isNo) {
              arr.push(item)
            }
          }
        })
        this.selectList = arr
      }
      else {
        this.selectList = this.allList
      }
      this.showMenu = true
    },
    async getSelectList () { //根据filter获取过滤条件 获取所有员工
      if (!this.dropDownList.length > 0) {
        const res = await getStaffListForFilter({
          filter: this.filter,
          org_level_uuid: this.$store.state.currentCompany
        })
        if (res.code == 200) {
          if (!this.allList.length > 0) {
            this.allList = res.data.map(item => {
              item.checked = false
              return item
            })
          }
        }
      }
      else {
        this.allList = this.dropDownList.map(item => {
          item.checked = false
          return item
        })
      }
    },
    getStaffInfo () {
      if (!this.allList.length > 0) this.getSelectList()
      if (this.multiple) { //多选
        if (this.staffUuid == '' || !this.staffUuid) {
          this.selectList = this.selectList.map(item => {
            item.checked = false
            return item
          })
          return this.viewList = []
        }
        const res = this.staffUuid.split(',')
        this.viewList = []
        this.allList.map(item => {
          item.checked = false
          res.map(item1 => {
            if (item1 == item.uuid) {
              item.checked = true
              this.viewList.push(item)
            }
          })
        })
        this.$emit('changeStaffList', this.viewList)
      }
      else {//单选
        if (this.staffUuid == '' || !this.staffUuid) return this.viewList = {}
        this.viewList = {}
        this.allList.map(item => {
          if (this.staffUuid == item.uuid) {
            this.viewList = item
          }
        })
        this.$emit('changeStaffList', this.viewList)
      }
    },

  },
};
</script>

<style scoped>
</style>
